<script>
import DataService from '../../../../services/DataService';

export default {
    props: {
        checkVueodolist:null,
            projectid: {
                type: String,
                required: true
            },
        },
        data: function () {
            return {
                isLoading:false,
                AllocationModal: null,
                task_Id: null,
                projectid: null,
                taskTeams: [],
                selectedTaskTeam: [],
                noRecordTask: true,
                isSearch: true,
                checkTaskAll: false,
                AllocatedHours: null,
                searchText: '',
                currencyName: '',
                StartDate: null,  //////////07-08-2019//////////
                EndDate: null,
                DayStartTime: null,
                DayStopTime: null,
                PerDayWorkingHours: null,
                HourPerDay: null,
                MinutesPerDay: 0,
                TaskType: null,
                Description: '',
                StartTime: null,
                EndTime: null,
                FromTimeError: true,
                PlanStartDate: null,
                PlanEndDate: null,
                //AssignedBy: null,
                datePickerMaxConfig: {
                    format: String('M/dd/yyyy').toUpperCase(),
                    useCurrent: false,
                    showClear: true,
                    showClose: true,
                    allowInputToggle: true,
                    widgetPositioning: {
                        horizontal: 'left',
                        vertical: 'bottom'
                    }
                },
                datePickerMaxConfigTop: {
                    format: String('M/dd/yyyy').toUpperCase(),
                    useCurrent: false,
                    showClear: true,
                    showClose: true,
                    allowInputToggle: true,
                    widgetPositioning: {
                        horizontal: 'left',
                        vertical: 'top'
                    }
                },
                timePickerMaxConfig: {
                    //format: String('h:mm tt').replace(":", "").replace("m", "").replace("m", "").replace("t", "A").replace("t", ""),
                    format: $("#hdnTimeFormat").val() == '24' ? 'HH:mm' : String('h:mm tt').replace("t", "A").replace("t", ""),
                    useCurrent: false,
                    showClear: true,
                    showClose: true,
                    allowInputToggle: true,
                    widgetPositioning: {
                        horizontal: 'left',
                        vertical: 'bottom'
                    }
                },
                timePickerMaxConfigTop: {
                    //format: String('h:mm tt').replace(":", "").replace("m", "").replace("m", "").replace("t", "A").replace("t", ""),
                    format: $("#hdnTimeFormat").val() == '24' ? 'HH:mm' : String('h:mm tt').replace("t", "A").replace("t", ""),
                    useCurrent: false,
                    showClear: true,
                    showClose: true,
                    allowInputToggle: true,
                    widgetPositioning: {
                        horizontal: 'left',
                        vertical: 'top'
                    }
                },
                selectedHours: [],
                selectOptionsHours: [
                  { Hourvalue: null, name: 'Select Hour' },
                  { Hourvalue: '0', name: '00' },
                  { Hourvalue: 1, name: '01' },
                  { Hourvalue: 2, name: '02' },
                  { Hourvalue: 3, name: '03' },
                  { Hourvalue: 4, name: '04' },
                  { Hourvalue: 5, name: '05' },
                  { Hourvalue: 6, name: '06' },
                  { Hourvalue: 7, name: '07' },
                  { Hourvalue: 8, name: '08' },
                  { Hourvalue: 9, name: '09' },
                  { Hourvalue: 10, name: '10' },
                  { Hourvalue: 11, name: '11' },
                  { Hourvalue: 12, name: '12' },
                  { Hourvalue: 13, name: '13' },
                  { Hourvalue: 14, name: '14' },
                  { Hourvalue: 15, name: '15' },
                  { Hourvalue: 16, name: '16' },
                  { Hourvalue: 17, name: '17' },
                  { Hourvalue: 18, name: '18' },
                  { Hourvalue: 19, name: '19' },
                  { Hourvalue: 20, name: '20' },
                  { Hourvalue: 21, name: '21' },
                  { Hourvalue: 22, name: '22' },
                  { Hourvalue: 23, name: '23' },
                  { Hourvalue: 24, name: '24' }
                ],
                selectedMinutes: [],
                selectOptionsMinutes: [
                  { Minutevalue: null, name: 'Select Minute' },
                  { Minutevalue: 0, name: '00' },
                  { Minutevalue: 15, name: '15' },
                  { Minutevalue: 30, name: '30' },
                  { Minutevalue: 45, name: '45' },

                ],
                DependentOption: [{
                    format: String('M/dd/yyyy').toUpperCase(),
                    useCurrent: false,
                    showClear: false,
                    showClose: false,
                    allowInputToggle: true,
                    minDate: new Date(Date.now())
                },
                  {
                      format: String('M/dd/yyyy').toUpperCase(),
                      useCurrent: false,
                      showClear: false,
                      showClose: false,
                      allowInputToggle: true
                  }
                ],
                tsDate: null,
                teDate: null,
                AllocatedTaskHours: null,
            }
        },
        created: function () {
            var vm = this;
            vm.task_Id = checkVueodolist.modalAddEditTaskId;
            vm.projectid = checkVueodolist.ProjectId;
            vm.selectedTaskTeam = checkVueodolist.TaskTeam;
            vm.AllocatedHours = checkVueodolist.itemHours;
            vm.AllocatedTaskHours = checkVueodolist.TaskHours;
            vm.tsDate = checkVueodolist.sDate;
            vm.teDate = checkVueodolist.eDate;
            if (typeof vm.task_Id == 'undefined' || vm.task_Id == null) {
                vm.task_Id = 0;
            }
            if (vm.task_Id != null && vm.projectid != null) {
                vm.FetchData(vm.task_Id, vm.projectid, vm.selectedTaskTeam);
            }

            vm.currencyName = '@loggedUserInfo.Currency';
            //vm.currencyName = localStorage.getItem('currency');
            vm.AllocationModal = vm.$parent.AllocationModal;
            vm.GetSelectOptionsMinutes();
        },
        methods: {
            CheckTeamHours: function (item) {
                //
                var vm = this;
                var value = item.Taskhours;
                if (item.PlanStartDate != null && item.PlanStartDate != '' && item.PlanEndDate != null && item.PlanEndDate != '') {
                    //var totalDays = this.DaysBetweenDates(new Date(item.PlanStartDate), new Date(item.PlanEndDate));
                    var totalDays = this.GetProjectWorkingDays(vm.projectid, item.PlanStartDate, item.PlanEndDate);
                    var ctrl = $("hdnSpecificTime" + item.UserId).closest("tr");
                    var Hours = Number(item.HourPerDay);
                    if ($("#hdnSpecificTime" + item.UserId).val() == "1" || $("#txtFromTimePerDay_" + item.UserId).is(":visible") == true) {
                        $("#hdnSpecificTime" + item.UserId).val("1");
