                        var endTime = $("#txtToTimePerDay_" + item.UserId).val();
                        var time = this.TimeDifference(item.PlanStartDate, startTime, endTime);
                        var totalHours = Number(time) * Number(totalDays)
                        if (item.Taskhours < totalHours) {
                            item.Taskhours = totalHours;
                        }
                    }
