            TimeDifference: function (fromDate, startTime, endTime) {
                //
                var date1 = new Date(fromDate + " " + startTime);
                var date2 = new Date(fromDate + " " + endTime);

                var diff = date2.getTime() - date1.getTime();

                var msec = diff;
                var hh = Math.floor(msec / 1000 / 60 / 60);
                msec -= hh * 1000 * 60 * 60;
                var mm = Math.floor(msec / 1000 / 60);
                msec -= mm * 1000 * 60;
                var ss = Math.floor(msec / 1000);
                msec -= ss * 1000;
                if (mm > 0) {
                    return Number(hh) + 1;
                }
                return hh;
            },
            DaysBetweenDates: function (fromDate, toDate) {
                // hours*minutes*seconds*milliseconds
                var oneDay = 24 * 60 * 60 * 1000;
                var firstDate = new Date(fromDate.setHours(0, 0, 0, 0));
                var secondDate = new Date(toDate.setHours(0, 0, 0, 0));
                var diffDays = Math.round(Math.abs((firstDate.getTime() - secondDate.getTime()) / (oneDay)));
                return diffDays + 1;
            },
