            hoursColon: function (item) {
                vm = this;
                //////Start Remove Extra colon from Hours 25-07-2019
                var colon = ":";
                var str = item.Taskhours;
                for (var i = 0; i < colon.length; i++) {
                    var char = colon.charAt(i);
                    if (str.split(char).length > 2) {
                        item.Taskhours = str.substring(0, str.lastIndexOf(":"));
                    }
                }
                /////End Remove Extra colon from Hours 25-07-2019
                var isColon = item.Taskhours;

                if (isColon.indexOf(':') != -1) {
                    var formatcolon = isColon.split(':');
                    var colhrs = formatcolon[0];
                    var colmin = formatcolon[1];
                    if (colhrs > 9999999999) {
                        vm.ShowAlert(vm.$t("HoursExceedWarning"), "warning", true, vm.$t("Alert"));
                        return '';
                    }
                    var numbers = /[0]/;
                    if (colhrs < 9 && parseInt(colhrs, 10) != 0) {
                        colhrs = "0" + colhrs;
                        colhrs = colhrs.split("00").join("");
                        if (!colhrs.match(numbers)) {
                            colhrs = "0" + colhrs;
                        }
                    }
                    else if (parseInt(colhrs, 10) == 0) {
                        colhrs = '00';
                    }
                    if (colmin < 9 && parseInt(colmin, 10) != 0) {
                        colmin = "0" + colmin;
                        colmin = colmin.split("00").join("");
                        if (!colmin.match(numbers)) {
                            colmin = "0" + colmin;
                        }
                    }
                    else if (parseInt(colmin, 10) == 0) {
                        colmin = '00';
                    }
                    if (colmin == "") {
                        return colhrs + ':00';
                    }
                    else {
                        if (colmin > 59) {
                            return colhrs + ':59';
                        } else {
                            return colhrs + ':' + colmin;
                        }
                    }
                    return false;
                }
                var hrs = parseInt(Number(item.Taskhours), 10);
                if (hrs > 9999999999) {
                    this.ShowAlert(this.$t("HoursExceedWarning"), "warning", true, this.$t("Alert"));
                    return '';
                }
                var min = Math.round((Number(item.Taskhours) - hrs) * 60);
                if (hrs <= 9)
                    hrs = "0" + hrs;
                if (min <= 9)
                    min = "0" + min;
                var hourTotal = hrs + ':' + min;
                if (hourTotal == '00:00') {
                    item.Taskhours = '';
                }
                else {
                    item.Taskhours = hourTotal;
                }

            },
            CommonConvertHHmmToSeconds: function (hhmm) {
                try {
                    if (String(hhmm).includes(":")) {
                        var totaltime = hhmm.split(":");
                        var hours = parseInt(totaltime[0], 10) * 60 * 60;
                        var minutes = parseInt(totaltime[1], 10) * 60;
                        var convertedSeconds = hours + minutes;
                        return convertedSeconds;
                    }
                    else {
                        var totaltime = hhmm;
                        var hours = parseInt(hhmm) * 60 * 60;
                        return hours;
                    }
                }
                catch (ex) {
                    return hhmm;
                }
            },
