                        else {
                            var startTime = new Date("2020/01/01 " + String(teamItem.DayStartTime).toLowerCase());
                            var endTime = new Date("2020/01/01 " + String(teamItem.DayStopTime).toLowerCase());
                            //
                            if (endTime < startTime) {
                                endTime.setDate(endTime.getDate() + 1);
                            }
                            startTime.setMinutes(0);
                            endTime.setMinutes(0);

                            var diffMs = (endTime - startTime);
                            perDayWorkingHours = Math.floor((diffMs / 1000) / 60);
                            //
                        }

                        checkVueodolist.Team.push({
                            TaskId: vm.task_Id,
                            ProjectId: vm.projectid,
                            UserId: teamItem.UserId,
                            UserName: teamItem.MemberName,
                            Rate: teamItem.Rate,
                            TaskHours: vm.CommonConvertHHmmToSeconds(teamItem.Taskhours),
                            StartDate: teamItem.PlanStartDate == '' || teamItem.PlanStartDate == null ? null : moment(teamItem.PlanStartDate).format('MM/DD/YYYY'),
                            EndDate: teamItem.PlanEndDate == '' || teamItem.PlanEndDate == null ? null : moment(teamItem.PlanEndDate).format('MM/DD/YYYY'),
                            DayStartTime: teamHoursType == "1" ? teamItem.DayStartTime : null,
                            DayStopTime: teamHoursType == "1" ? teamItem.DayStopTime : null,
                            PerDayWorkingHours: perDayWorkingHours,
                            TaskType: teamItem.TaskType,
                            Description: teamItem.Description,
                            ShowDayTime: teamItem.ShowDayTime > 0 ? 1 : 0
                        });
                    }));
                    var objectPostString = JSON.stringify(checkVueodolist.Team);
                    DataService.SaveTaskTeam(objectPostString).then(function (response) {                       
                        //
                        if (response.data.result == "0") {
                            vm.ShowAlert(vm.$t("TaskAssignedToTeam"), "success", true, vm.$t("Alert"));
                            checkVueodolist.FetchDataKanban('kanban', null);
                            this.$emit('close');
                            vm.isLoading=false;
                        }
                        else {
                            vm.ShowAlert(vm.$t("ProcessingError"), "failure", true, vm.$t("Alert"));
                            vm.isLoading=false;
                        }
                        vm.isLoading=false;
                    });
                    // setTimeout("ShowPopoverData()", 500);
                }
                else {
                    vm.ShowAlert(vm.$t("CorrectHighlightedErrors"), "warning", true, vm.$t("Alert"));
                }
            },
            ConvertHoursToMinutes: function (hhmm) {
                if (String(hhmm).includes(":")) {
                    var totaltime = hhmm.split(":");
                    var hours = parseInt(totaltime[0], 10) * 60;
                    var minutes = parseInt(totaltime[1], 10);
                    var convertedMintues = hours + minutes;
                    return convertedMintues;
                }
                return hhmm;
            },
            CommonConvertDecimalToHHMM: function (Seconds, isHours) {
                // var isColon = timeInDecimal.val();
                var Hours = Math.floor(parseFloat(Seconds / 3600), 0);
                var Minutes = Math.floor(parseFloat((Seconds % 3600) / 60), 0);
                var Sec = Math.floor(parseFloat((Seconds % 3600) % 60), 0);
                if (Hours > 0) {
                    if (Hours < 10)
                        Hours = parseFloat("0" + Hours);
                }
                else
                    Hours = parseFloat("00");
                if (Minutes > 0) {
                    if (Minutes < 10 && Minutes > 0)
                        Minutes = parseFloat("00" + Minutes);
                }
                else
                    Minutes = "00";
                //if (Sec < 10)
                //    Sec = Convert.ToDouble("0" + Sec.ToString());

                if (isHours) {
                    return Hours;
                }
                else {
                    var TotalHours = Hours + ":" + Minutes;// +Sec.ToString() + "S";
                    return TotalHours;
                }
            },
            FetchData: function (taskId, projectid, selectedTaskTeam) {
                //
                var vm = this;
                var count = 0;
                DataService.AddMemberInTaskByTaskID('Projectid=' + projectid + '&taskId=' + taskId + '&search=&userIds=')
                .then(function(response){
                    if (response.status != null && response.status != "") {
                            vm.taskTeams = response.status;
                            var dateFormat = String('M/dd/yyyy').toUpperCase();
                            var chkTeam = "";
                            var timeFormat = 'h:mm tt';
                            timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
                            if ($("#hdnTimeFormat").val() == '24') {
                                timeFormat = 'HH:mm';
                            }
                            $.each(vm.taskTeams, $.proxy(function (index, childItem1) {
                                childItem1.isTaskSelected = false;
                                childItem1.disabled = false;
                                childItem1.isSearch = true;
                                childItem1.RowIndex = "RowIndexGlobal_" + index;
                                var currentTime = moment.utc().toDate();
                                childItem1.DayStartTime = moment(currentTime).format(timeFormat);
                                var addHourCurrentTime = moment.utc().toDate();
                                childItem1.DayStopTime = moment(addHourCurrentTime).add(1, 'hours').format(timeFormat);
                                if (childItem1.Taskhours == null || childItem1.Taskhours == '') {
                                    childItem1.Taskhours = '0';
                                }
                                var result = $.grep(selectedTaskTeam, function (element, index) {
                                    return element.UserId == childItem1.UserId;
                                });
                                childItem1.MinutesPerDay = '00';
                                $.each(result, $.proxy(function (index, TaskTeam) {
                                    chkTeam = "yes";
                                    if (childItem1.UserId == TaskTeam.UserId) {
                                        childItem1.isTaskSelected = true;
                                        count++;
                                        if (TaskTeam.TaskHours != null && TaskTeam.TaskHours != '' && TaskTeam.TaskHours > 0) {
                                            childItem1.Taskhours = vm.CommonConvertDecimalToHHMM(TaskTeam.TaskHours, true);
                                        }
                                        else {
                                            childItem1.Taskhours = '0';
                                        }
                                        if (TaskTeam.Rate != null && TaskTeam.Rate != '') {
                                            childItem1.Rate = TaskTeam.Rate;
                                        }
                                        if (TaskTeam.StartDate != null && TaskTeam.StartDate != '') {
                                            childItem1.PlanStartDate = moment(TaskTeam.StartDate).format(dateFormat);
                                        }
                                        else {
                                            childItem1.PlanStartDate = moment(vm.AllocationModal.PlanStartDate).format(dateFormat);
                                        }

                                        if (TaskTeam.EndDate != null && TaskTeam.EndDate != '') {
                                            childItem1.PlanEndDate = moment(TaskTeam.EndDate).format(dateFormat);
                                        }
                                        else {
                                            childItem1.PlanEndDate = moment(vm.AllocationModal.PlanEndDate).format(dateFormat);
                                        }

                                        if (TaskTeam.TaskType != null && TaskTeam.TaskType != '') {
                                            childItem1.TaskType = TaskTeam.TaskType;
                                        }
                                        if (TaskTeam.Description != null && TaskTeam.Description != '') {
                                            childItem1.Description = TaskTeam.Description;
                                        }
                                        childItem1.ShowDayTime = 0;
                                        if (TaskTeam.DayStartTime != null) {
                                            childItem1.ShowDayTime = 1;
                                            var dst = moment(TaskTeam.DayStartTime).format(timeFormat);
                                            if (dst.trim() != 'Invalid date') {
                                                childItem1.DayStartTime = moment(TaskTeam.DayStartTime).format(timeFormat);
                                            }
                                            else {
                                                childItem1.DayStartTime = TaskTeam.DayStartTime;
                                            }
                                        }

                                        if (TaskTeam.DayStopTime != null) {
                                            childItem1.ShowDayTime = 1;
                                            var dst = moment(TaskTeam.DayStopTime).format(timeFormat);
                                            if (dst.trim() != 'Invalid date') {
                                                childItem1.DayStopTime = moment(TaskTeam.DayStopTime).format(timeFormat);
                                            }
                                            else {
                                                childItem1.DayStopTime = TaskTeam.DayStopTime;
                                            }
                                        }
                                        //
                                        var chkdata = true;
                                        if (TaskTeam.PerDayWorkingHours > 0) {
                                            chkdata = false;
                                            var minutesPerDay = Math.floor(TaskTeam.PerDayWorkingHours % 60);
                                            var chkHourPerDay = Math.floor(TaskTeam.PerDayWorkingHours / 60);
                                            /* if (minutesPerDay > 0) {
                                                 childItem1.HourPerDay = Math.floor(TaskTeam.PerDayWorkingHours / 60);
                                                 childItem1.MinutesPerDay = minutesPerDay < 10 ? "0" + minutesPerDay : minutesPerDay;
                                             }*/
                                            if (chkHourPerDay > 0) {
                                                childItem1.HourPerDay = Math.floor(chkHourPerDay);
                                            }
                                            else {
                                                childItem1.HourPerDay = 0;
                                            }
                                        }
                                        else {
                                            childItem1.HourPerDay = 0;
                                        }
                                        if (TaskTeam.DayStopTime != null && TaskTeam.DayStopTime != null) {
                                            chkdata = false;
                                            childItem1.HourPerDay = childItem1.HourPerDay > 0 ? childItem1.HourPerDay : 0;
                                            childItem1.MinutesPerDay = '00';
                                        }
                                        //
                                        if (chkdata) {
                                            childItem1.MinutesPerDay = '00';
                                        }
                                        //vm.AssignedBy=childItem1.AssignedBy;
                                    }
                                }));
                                if (chkTeam == "") {
                                    childItem1.PlanStartDate = moment(vm.AllocationModal.PlanStartDate).format(dateFormat);
                                    childItem1.PlanEndDate = moment(vm.AllocationModal.PlanEndDate).format(dateFormat);
                                }
                            }));

                            if (vm.taskTeams.length == count) {
                                vm.checkTaskAll = true;
                            }
                            vm.noRecordTask = false;
                        }
                        setTimeout("CheckBoxBootstrap()", 100);
                        setTimeout("RadioButtonBootstrap()", 400);
                        setTimeout(function () {
                            $(".table-fix-header").off('scroll').on("scroll", function (event) {
                                event.stopImmediatePropagation();
                                $(this).find("input:text").trigger('blur');
                                $(".bootstrap-datetimepicker-widget").remove();
                            });
                        }, 500);
                });
            },
        },
}
</script>