<template>
<div>
    <loader :is-visible="isLoading"></loader>

    <div class="modal my-popups right fade" data-keyboard="false" data-backdrop="static" id="modalprojectquickviewpop" role="dialog" v-cloak>
    
        <div class="modal-dialog maxwidth500" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <button type="button" style="height:56px;" class="close" v-on:click="CloseQuickView()" data-dismiss="modal"><i class="fa fa-times" aria-hidden="true"></i></button>
                    <h4 class="modal-title text-truncate" style="line-height:26px;" id="myModalLabel">{{TASK_NAME}} {{QuickViewType}}</h4>
                </div>
                <div class="modal-body">
                    <div class="white_bx">
                        <div class="col-lg-12 p-0">
                            <div v-if="IsViewResourcesClicked==false">
                                <div class="heading-border mb-3" id="status" v-if="QuickViewType != 'attachment'">
                                    <h5 class="h5">
                                        <span>{{$t('Progress')}}</span>
                                        <div class="btnnot px-2 text-white d-block text-center" v-bind:style="{backgroundColor: STATUS_BG_COLOR ,color: STATUS_COLOR}">{{ProjectStatus}}</div>
                                    </h5>
                                </div>
                                <div class="level_info mb-3" v-if="QuickViewType != 'attachment'">
                                    <ul>
                                        <li><b style="color:#23a5fd">{{AllocateHours}}</b> {{$t('AllocatedHours')}}</li>
                                        <li><b style="color:#28a745">{{TrackHours}}</b>{{$t('TrackedHours')}}  </li>
                                    </ul>
                                    <div class="progress prog_bx">
                                        <div class="progress-bar" v-bind:class="{'bg-sky':ExtraHours > 0,'bg-red':ExtraHours < 0}" v-bind:style="{width:HoursDonePercentage +'%'}" role="progressbar" aria-valuenow="0" aria-valuemin="0" aria-valuemax="100">
                                        </div>                                        
                                    </div>
                                    <ul v-if="ExtraHours < 0">
                                        <li><b style="color:#ff0000">{{ExceedHours}}</b> Exceed Hours</li>
                                    </ul>
                                </div>
                            </div>
                            <div class="col-lg-12 p-0">
                                <div v-if="IsViewResourcesClicked==false">
                                    <div class="heading-border mb-3" id="status" v-if="QuickViewType != 'attachment'">
                                        <h5 class="h5"><span>{{$t('Details')}}</span></h5>
                                    </div>
                                    <ul class="detail_list" v-if="QuickViewType != 'attachment'">
                                        <li><b> {{$t('ProjectName')}}:</b> <span> {{(PROJECT_NAME=='CRM_LEADS'?'CRM Lead':PROJECT_NAME=='CRM_DEALS'?'CRM Deal':PROJECT_NAME=='CRM_SALES_ORDERS'?'CRM SALES ORDER':PROJECT_NAME=='CRM_QUOTES'?'CRM QUOTES':PROJECT_NAME)}}</span></li>
                                        <li><b> {{$t('ProjectManager')}}:</b>  <span v-bind:title="PM">{{PM}}</span></li>
                                        <li><b>{{$t('Isbillable')}}:</b> <span> {{IS_BILLABLE == false ? 'No' : 'Yes'}}</span></li>
                                        <li><b>{{$t('SAParent')}}{{$t('TaskName')}} :</b> <span> {{PARENT_TASK}}</span></li>
                                        <li><b>{{$t('TaskName')}}:</b> <span> {{TASK_NAME}}</span></li>
                                        <li><b>{{$t('StartDateTime')}}:</b> <span> {{formatDate(START_DATE)}}</span></li>
                                        <li><b>{{$t('EndDateTime')}}:</b>  <span>{{formatDate(END_DATE)}}</span></li>
                                        <li><b>{{$t('TaskDescription')}}:</b> <span> {{TASK_DESCRIPTION}}</span></li>
                                        <li><b>{{$t('TaskRate')}}:</b> <span> {{TASK_RATE}}</span></li>
                                    </ul>
                                </div>
                                <div class="heading-border mb-3" id="status" v-if="QuickViewType != 'attachment'">
                                    <h5 class="h5"><span>{{$t('Details')}}</span></h5>
                                </div>
                            <vue-scroll>
                                <div id="divscrollsetuptabs" style="max-height:300px;" class="p-0 col-12" v-if="QuickViewType != 'attachment'">
                                    <div class="profile-info mb-3" v-for="item in ProjectTeam" :key="item">
                                        <span class="my-image d-none d-lg-block" v-if="item.Image_Url!=null"><img v-bind:src="item.Image_Url" /></span>
                                        <span class="my-image d-none d-lg-block" v-else><img src="/Content/images/DefaultImages/uc_user_new.jpg" /></span>
                                        <span class="m-text">{{item.Team_Name}}</span>
                                    </div>
                                    <div class="text-center text-danger d-flex align-items-center justify-content-center borderbababa p-2 mb-4 no-record" v-if="ProjectTeam == ''">
                                       {{$t('NoRecordfound')}}
                                    </div>
                                </div>
                            </vue-scroll>
                                <div v-if="IsViewResourcesClicked==false">
                                    <div class="heading-border mb-3 pt-2" id="status">
                                        <h5 class="h5"><span> {{$t('Files')}}</span></h5>
                                        <div class="text_link">
                                            <!-- <a href="javascript:;" v-if="ShowDiv" v-on:click="openUploadFileModel()"><i class="fa fa-paperclip pr-2"></i>{{$t('Add')}}{{$t('File')}}</a>
                                            <a href="javascript:;" v-if="!ShowDiv" v-on:click="BackDetail()"><i class="fa fa-angle-double-left pr-2"></i>{{$t('File')}}</a> -->
                                        </div>
                                    </div>
                                    <div class="w-100 d-none" id="divscrollsetupfile" style="max-height: 200px; overflow-y:auto;">
                                        <a class="p-1 m-1 d-block" v-on:click="DownloadFile(item)" v-bind:title="item.LATEST_FILE_NAME" href="javascript:;" 
                                        v-for="(item,index) in fileData" :key="index">
                                            <span v-if="!noRecordForDetail">{{item.LATEST_FILE_NAME}} ({{item.LATEST_VERSION}})</span>
                                        </a>
                                        <div v-if="noRecordForDetail" class="text-center text-danger d-flex align-items-center justify-content-center borderbababa p-2 mb-4 no-record">{{$t('NoRecordfound')}}</div>
                                    </div>
                                    <ul class="file-list" v-if="ShowDiv">
                                        <li v-on:click="GetDetail('IMAGES',projectId)" style="cursor:pointer">
                                            <span class="fldr-icon"><i class="fa fa-folder"></i></span>
                                            <span class="m-text">{{$t('Image')}}</span>
                                            <small>{{Images}} Items</small>
                                            <a href="javascript:;" class="right-acti d-none"><i class="fa fa-ellipsis-h"></i></a>
                                        </li>
                                        <li v-on:click="GetDetail('DOCUMENTS',projectId)" style="cursor:pointer">
                                            <span class="fldr-icon"><i class="fa fa-folder"></i></span>
                                            <span class="m-text">{{$t('Documents')}}</span>
                                            <small>{{Documents}} Items</small>
                                            <a href="javascript:;" class="right-acti d-none"><i class="fa fa-ellipsis-h"></i></a>
                                        </li>
                                        <li v-on:click="GetDetail('VIDEOS',projectId)" style="cursor:pointer">
                                            <span class="fldr-icon"><i class="fa fa-folder"></i></span>
                                            <span class="m-text">Videos</span>
                                            <small>{{Videos}} Items</small>
                                            <a href="javascript:;" class="right-acti d-none"><i class="fa fa-ellipsis-h"></i></a>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../../../services/DataService';
export default {
    props:{
        checkVueodolist:null
    },
    data: function () {
            return {
                isLoading:false,
                PROJECT_NAME: '',
                ProjectStatus: '',
                ACTIVE_TASK: '',
                IS_BILLABLE: '',
                PARENT_TASK: '',
                TASK_DESCRIPTION: '',
                TASK_NAME: '',
                TASK_RATE: '',
                START_DATE: '',
                END_DATE: '',
                PM: '',
                Images: '',
                Videos: '',
                Documents: '',
                ProjectTeam: [],
                STATUS_COLOR: '',
                STATUS_BG_COLOR: '',
                secondsPerMinute: 60,
                minutesPerHour: 60,
                AllocateHours: 0,
                AllocateHoursSecond: 0,
                TrackHours: 0,
                TrackHoursSecond: 0,
                HoursDonePercentage: 0,
                HoursToDoPercentage: 0,
                ProjectHours: 0,
                ExtraHours: 0,
                ExceedHours: 0,
                projectId: 0,
                taskId: 0,
                PageSize: 10,
                PageNumber: 1,
                ModelSortBy: '',
                ModelSortExp: '',
                noRecordForDetail: false,
                fileData: [],
                ShowDiv: true,
                QuickViewType: '',
                IsViewResourcesClicked: this.checkVueodolist.IsViewResourcesClicked
            }
        },
        created: function () {
            
            console.log('IsViewResourcesClicked:',this.checkVueodolist.IsViewResourcesClicked);
            if (this.$parent.modalProjectId != null) {
                this.projectId = this.$parent.modalProjectId;
                this.taskId = this.$parent.modalTaskId;
                this.QuickViewByProjectId(this.$parent.modalProjectId, this.$parent.modalTaskId);
            }
            this.QuickViewType = this.checkVueodolist.QuickViewType;
            var vm=this;
            setTimeout(function () {
                $("#modalprojectquickviewpop").modal('show');
                setTimeout(function () {
                    vm.SetBootstraptoolTip();
                }, 500);
            }, 500);
        },
        methods: {
            GetPrivilege: function(params){
            
            DataService.checkPrivilege(params)
                .then(function (response) {
                    if (response.data) {

                    }
                });
            },
            DownloadFile: function (item) {
                var vm = this;
                DataService.downloadProjectFiles('fileId='+item.FILE_VERSION_ID).then(function(response){

                })
            },
            formatDate: function (value) {
                if (value) {
                    try {
                        var timeFormat ="h:mm tt";// Globalize.culture().calendar.patterns.t;
                        timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
                        var getDate = new Date(moment(String(value)).year(), moment(String(value)).month(), moment(String(value)).date(), moment(String(value)).hours(), moment(String(value)).minutes());
                        //return moment(getDate, "YYYY-MM-DD HH:mm:ss").format(String(Globalize.culture().calendar.patterns.d).toUpperCase() + " " + String(timeFormat));
                        return moment(getDate, "YYYY-MM-DD HH:mm:ss").format(String('M/d/yyyy').toUpperCase());

                    } catch (e) {
                        var timeFormat = "h:mm tt";
                        timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
                        return moment(String(value)).format(String('M/d/yyyy').toUpperCase() + " " + String(timeFormat));
                    }
                }
            },            
            BackDetail: function () {
                var vm = this;
                vm.ShowDiv = true;
                vm.fileData = [];
                vm.noRecordForDetail = false;
                $("#divscrollsetupfile").removeClass("d-block");
                $("#divscrollsetupfile").addClass("d-none");
            },
            openUploadFileModel: function () {
                if (this.GetPrivilege('controller=ProjectFiles&action=Create')) {
                    $("#hdnUploadNewFilePopUp").val('C').attr('url', "/ProjectFiles/Create?projectId=" + this.$parent.modalProjectId);
                    $("#divuploadnewfile").dialog({ height: 'auto', width: '84%', title: this.$t("UploadFile") });
                    $("#divuploadnewfile").dialog("open");
                }
            },
            GetDetail: function (type, projectId) {
                var vm = this;
                vm.ShowDiv = false;
                vm.fileData = [];
                var param ="projectId=" + projectId + "&pageSize=" + vm.PageSize + "&pageNum=" + vm.PageNumber + "&sortBy=" + vm.ModelSortBy + "&sortExp=" + vm.ModelSortExp + "&viewType=" + type;
               DataService.FileListing(param).then(function (response) {
                   vm.isLoading=false;
                    if (response.data != null && response.data.status == "Success") {
                        if (response.data.result.data.length > 0) {
                            vm.fileData = response.data.result.data;
                            vm.noRecordForDetail = false;
                            $("#divscrollsetupfile").addClass("d-block");
                            $("#divscrollsetupfile").removeClass("d-none");
                        }
                        else {
                            vm.noRecordForDetail = true;
                            $("#divscrollsetupfile").addClass("d-block");
                            $("#divscrollsetupfile").removeClass("d-none");
                        }
                    } else {
                        vm.noRecordForDetail = true;
                        $("#divscrollsetupfile").addClass("d-block");
                        $("#divscrollsetupfile").removeClass("d-none");
                    }
                });
            },
            openUploadFileModel: function () {
                if (this.GetPrivilege('controller=ProjectFiles&action=Create')) {
                    $("#hdnUploadNewFilePopUp").val('C').attr('url', "/ProjectFiles/Create?projectId=" + this.$parent.modalProjectId);
                    $("#divuploadnewfile").dialog({ height: 'auto', width: '84%', title: this.$t("UploadFile") });
                    $("#divuploadnewfile").dialog("open");
                }
            },
            CloseQuickView: function () {
                var vm = this;
                vm.$parent.ShowManageProjectQuickView = false;
            },
            GetExtraHours: function () {
                var vm = this;
                var ehours = vm.AllocateHoursSecond - vm.TrackHoursSecond
                return ehours;
            },
            GetPercentage: function () {
                var vm = this;
                var val = 100;
                var percentage = vm.ExtraHours / vm.AllocateHoursSecond * 100;
                return (val - percentage);
            },
            QuickViewByProjectId: function (project_id, task_id) {
                var vm = this;
                var param = "projectId=" + project_id + "&taskId=" + task_id;
                DataService.GetQuickViewByProjectIdTaskId(param).then(function (response) {
                     
                    if (response.data.code != '0') {
                        if (response.data.result.project_detail.length > 0) {
                            vm.PROJECT_NAME = response.data.result.project_detail[0].PROJECT_NAME == null ? '--' : response.data.result.project_detail[0].PROJECT_NAME;
                            vm.ProjectStatus = response.data.result.project_detail[0].ProjectStatus;
                            vm.ACTIVE_TASK = response.data.result.project_detail[0].ACTIVE_TASK;
                            vm.IS_BILLABLE = response.data.result.project_detail[0].IS_BILLABLE;
                            vm.PARENT_TASK = response.data.result.project_detail[0].PARENT_TASK;
                            vm.TASK_NAME = response.data.result.project_detail[0].TASK_NAME;
                            vm.TASK_RATE = response.data.result.project_detail[0].RATE;
                            vm.TASK_DESCRIPTION = response.data.result.project_detail[0].TASK_DESCRIPTION;
                            vm.START_DATE = response.data.result.project_detail[0].START_DATE == 'null' ? '--' : response.data.result.project_detail[0].START_DATE;
                            vm.END_DATE = response.data.result.project_detail[0].END_DATE == 'null' ? '--' : response.data.result.project_detail[0].END_DATE;
                            vm.STATUS_COLOR = response.data.result.project_detail[0].STATUS_COLOR;
                            vm.STATUS_BG_COLOR = response.data.result.project_detail[0].STATUS_BG_COLOR;
                            vm.PM = response.data.result.project_detail[0].PM == null ? '--' : response.data.result.project_detail[0].PM;
                        }
                        vm.Images = response.data.result.project_files[0].Images;
                        vm.Videos = response.data.result.project_files[0].Videos;
                        vm.Documents = response.data.result.project_files[0].Documents;
                        vm.ProjectTeam = response.data.result.project_team;
                        //
                        //vm.ProjectHours = response.data.result.project_hours.length == 0 ? 0 : vm.convertSecondsToHHMMSSNew(response.data.result.project_hours[0].PROJECT_HOURS);
                        vm.AllocateHours = response.data.result.project_hours.length == 0 ? 0 : vm.convertSecondsToHHMMSSNew(response.data.result.project_hours[0].TOTAL_TASK_HOURS);
                        vm.TrackHours = response.data.result.project_hours.length == 0 ? 0 : vm.convertSecondsToHHMMSSNew(response.data.result.project_hours[0].TRACK_SECONDS);
                        vm.AllocateHoursSecond = response.data.result.project_hours.length == 0 ? 0 : response.data.result.project_hours[0].TOTAL_TASK_HOURS;
                        vm.TrackHoursSecond = response.data.result.project_hours.length == 0 ? 0 : response.data.result.project_hours[0].TRACK_SECONDS;
                        vm.ExtraHours = response.data.result.project_hours.length == 0 ? 0 : vm.GetExtraHours(response.data.result.project_hours[0].REMAINING_SECONDS);
                        vm.ExceedHours = vm.convertSecondsToHHMMSSNew(vm.ExtraHours);
                        //vm.HoursToDoPercentage = response.data.result.project_hours.length == 0 ? 0 : response.data.result.project_hours[0].REMAINING_PERCENTAGE;
                        vm.HoursDonePercentage = response.data.result.project_hours.length == 0 ? 0 : vm.GetPercentage(response.data.result.project_hours[0].COMPLETED);

                        //-----Old-------
                        //vm.ProjectHours = response.data.result.project_hours.length == 0 ? 0 : vm.convertSecondsToHHMMSSNew(response.data.result.project_hours[0].PROJECT_HOURS);
                        //vm.HoursDone = response.data.result.project_hours.length == 0 ? 0 : vm.convertSecondsToHHMMSSNew(response.data.result.project_hours[0].TRACK_SECONDS);
                        //vm.HoursToDo = response.data.result.project_hours.length == 0 ? 0 : vm.convertSecondsToHHMMSSNew(response.data.result.project_hours[0].REMAINING_SECONDS);
                        //vm.ExtraHours = response.data.result.project_hours.length == 0 ? 0 : response.data.result.project_hours[0].REMAINING_SECONDS;
                        //vm.HoursToDoPercentage = response.data.result.project_hours.length == 0 ? 0 : response.data.result.project_hours[0].REMAINING_PERCENTAGE;
                        //vm.HoursDonePercentage = response.data.result.project_hours.length == 0 ? 0 : response.data.result.project_hours[0].COMPLETED;
                        //setTimeout("SetScrolbar('dark-thick', 'divscrollsetuptabs')", 500);
                        vm.SetScrolbar('',null, 'divscrollsetuptabs');
                        //SetScrolbar('dark-thick', "divscrollsetuptabs");
                    }
                });
            },
            convertSecondsToHHMMSSNew: function (intSecondsToConvert) {
                var hours = this.convertHours(intSecondsToConvert);
                var minutes = this.getRemainingMinutes(intSecondsToConvert);
                minutes = minutes == 60 ? "00" : minutes;
                var seconds = this.getRemainingSeconds(intSecondsToConvert);
                if (hours < 10 && hours > 0) hours = "0" + hours;
                if (minutes < 10 && minutes > 0) minutes = "0" + minutes;
                if (seconds < 10) seconds = "0" + seconds;
                if (minutes.toString().includes("-")) minutes = minutes.toString().replace("-", "");
                return hours + " h " + minutes + " m "
            },
            convertHours: function (intSeconds) {
                var hours = 0;
                var minutes1 = this.convertMinutes(intSeconds);
                var minutes = minutes1.toString().replace("-", "");
                hours = Math.floor(minutes / this.minutesPerHour);
                return hours
            },
            convertMinutes: function (intSeconds) {
                return Math.floor(intSeconds / this.secondsPerMinute)
            },
            getRemainingMinutes: function (intSeconds) {
                var intTotalMinutes = this.convertMinutes(intSeconds);
                return intTotalMinutes % this.minutesPerHour
            },
            getRemainingSeconds: function (intTotalSeconds) {
                return intTotalSeconds % this.secondsPerMinute
            },
        },
}
</script>