<template>
    <transition name="modal">
        <loader :is-visible="isLoading"></loader>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title"> Notes </h5>
                            <span class="float-right"><a href="javascript:;" class="text-white" title=""
                                    data-toggle-tooltip="tooltip" data-original-title="Card View"
                                    v-on:click="NoteListType = 'card'" v-if="NoteListType == 'list'"><i
                                        class="fa fa-th-large"></i> {{ $t('CardView') }} </a>
                                <a href="javascript:;" class="text-white" title="" data-toggle-tooltip="tooltip"
                                    data-original-title="List View" v-on:click="NoteListType = 'list'"
                                    v-if="NoteListType != 'list'"><i class="fa fa-list"></i> {{ $t('ListView') }} </a></span>
                            <button v-on:click="$emit('close')" href="javascript:;" class="close">
                                <i class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <div class="modal-body" style="max-height:600px;overflow-y:auto;">
                            <div class="col-12 p-0">
                                <div class="container-fluid p-0">
                                    <div style="clear:both;"></div>
                                    <div class="pb-2 d-inline-block">
                                        <span class="legend-Progress colorboxsquare"></span> {{ $t("Private") }} {{ $t("NOTE")
                                        }}
                                        <span class="legend-upcoming colorboxsquare ml-3"></span>{{ $t("Public") + ' ' +
                                            $t("NOTE") }}
                                    </div>
                                    <div v-if="NoRecordForTab" class="text-center text-danger p-2 border">
                                        {{ $t("NoRecordfound") }}</div>
                                    <div style="clear:both;"></div>
                                    <div v-if="NoteListType != 'list'"
                                        class="row ticket-card-container card-view bg-border p-0">
                                        <div style="min-width:257px" class="col-md-12 col-lg-6 col-xl-3 mb-3"
                                            v-for="item in NoteData" :key="item">
                                            <div v-if="!NoRecordForTab"
                                                v-bind:class="{ 'card-box': true, 'blue-bg-light': item.IS_PUBLIC, 'green-bg-light': !item.IS_PUBLIC }">
                                                <div class="card-heading d-flex align-items-center">
                                                    <span class="user-img-con mr-2">
                                                        <img v-bind:src="(item.USER_IMAGE_PATH != null && item.USER_IMAGE_PATH != '') ? item.USER_IMAGE_PATH : '/Content/images/uc/uc_user_new.jpg'"
                                                            alt="" /></span> {{ item.POSTED_BY }}
                                                </div>
                                                <div class="card-detail-main" style="max-height: 150px;">
                                                    <div class="card-detail" v-if="item.IS_PUBLIC == true"><i
                                                            class="fa fa-users" aria-hidden="true"></i><span>
                                                            <p>Public</p>
                                                        </span></div>
                                                    <div class="card-detail" v-if="item.IS_PUBLIC != true"><i
                                                            class="fa fa-users" aria-hidden="true"></i><span>
                                                            <p>Private</p>
                                                        </span></div>
                                                    <div class="card-detail"><span>
                                                            <p v-html="item.NOTE_CONTENT"></p>
                                                        </span></div>

                                                    <div class="bottom-action">
                                                        <div class="card-date"><i class="fa fa-calendar-o"
                                                                aria-hidden="true"></i>
                                                            <span>{{item.CREATED_AT| formatDateTime }}</span></div>
                                                        <div class="card-action" v-if="(userid == item.CREATED_BY)"> ||
                                                            (item.IS_PRIVATE == true)
                                                            <a href="javascript:;"
                                                                v-bind:class="{ 'disabled': disabledAddNote }"
                                                                class="round-icon-small btn-success-light"
                                                                v-on:click="OpenEditNote(item)">
                                                                <i class="fa fa-pencil" aria-hidden="true"></i>
                                                            </a>
                                                            <a href="javascript:;" class="round-icon-small btn-danger-light"
                                                                v-if="($parent.modalNoteTaskItem.LINKED_SOURCE_NAME.toUpperCase() != 'TICKETING')"
                                                                v-on:click="DeleteNote(item)">
                                                                <i class="fa fa-trash" aria-hidden="true"></i>
                                                            </a>
                                                        </div>
                                                    </div>

                                                </div>

                                            </div>
                                        </div>
                                    </div>

                                    <div class="conversation-main" v-if="NoteListType == 'list'">
                                        <template v-for="item in NoteData" v-if="!NoRecordForTab">
                                            <div class="conversation-time"><span>{{ timeFormatOnly(item.CREATED_AT)
                                            }}</span>
                                            </div>
                                            <div class="conversation-box-con">
                                                <div
                                                    v-bind:class="{ 'conversation-box': true, 'public-note': item.IS_PUBLIC, 'private-note': !item.IS_PUBLIC }">
                                                    <div class="name-circle light-grey"><img
                                                            class="img-circle avatar img-max-width"
                                                            v-bind:src="(item.USER_IMAGE_PATH != null && item.USER_IMAGE_PATH != '') ? item.USER_IMAGE_PATH : '/Content/images/uc/uc_user_new.jpg'"
                                                            alt="" /></div>
                                                    <div class="conversation-header">
                                                        <span>
                                                            <span class="sender-name">{{ item.POSTED_BY }}</span>
                                                            <span class="sender-department">(<small
                                                                    class="text-danger font-14"
                                                                    v-bind:title="item.CREATED_AT | formatDateTime">{{ item.CREATED_AT | formatDateTime}}</small>)
                                                            </span>
                                                        </span>
                                                        <span class=""><a
                                                                v-if="(userid == item.CREATED_BY && $parent.modalNoteTaskItem.LINKED_SOURCE_NAME.toUpperCase() != 'TICKETING')"
                                                                href="javascript:;"
                                                                class="round-icon-small btn-danger-light float-right"
                                                                v-on:click="DeleteNote(item)">
                                                                <i class="fa fa-trash" aria-hidden="true"></i>
                                                            </a>
                                                            <a v-if="(userid == item.CREATED_BY)" href="javascript:;"
                                                                v-bind:class="{ 'disabled': disabledAddNote }"
                                                                class="round-icon-small btn-success-light float-right"
                                                                v-on:click="OpenEditNote(item)">
                                                                <i class="fa fa-pencil" aria-hidden="true"></i>
                                                            </a></span>

                                                    </div>
                                                    <div class="conversation-content">
                                                        <p v-html="item.NOTE_CONTENT"></p>
                                                    </div>
                                                </div>
                                            </div>
                                        </template>
                                    </div>

                                    <div class="border-top-paging thispagebod-full"
                                        v-show="!NoRecordForTab && NoteData.length > 0">
                                        <pager-control v-bind:numberofrecords="TotalRecords"
                                            v-bind:callbackfunction="FetchNotes" :prev-text="$t(' Prev')"
                                            :first-text="$t('First')" :last-text="$t('Last')"
                                            :next-text="$t('Next')"></pager-control>
                                    </div>
                                    <div class="modal-footer pr-0">
                                        <div class="col-lg-6 text-right p-0 mr-0">
                                            <a href="javascript:;" v-bind:class="{ ' disabled': disabledAddNote }"
                                                class="btn btn-success formbtn text-white" v-on:click="OpenNote()">
                                                <i class="fa fa-plus pr-2"></i> {{ $t('AddNote') }}</a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
 
</template>
  
<script>
import DataService from '../../../../services/DataService';
export default {
    props: {
        notedata: {
            type: Array,
            required: true
        },

        userid: {
            type: Number,
            required: true
        },
        companyid: {
            type: Number,
            required: true
        },
        projectid: {
            type: Number,
            required: true
        },
        ispublic: {
            type: Boolean,
            required: true
        },
        pageSize: {
            type: Number,
            required: true
        },
        pageNum: {
            type: Number,
            required: true
        },
        sortBy: {
            type: String,
            required: true
        },
        sortExp: {
            type: String,
            required: true
        },
        numberofrecords: {
            type: Number,
            required: true
        },
    
        reqfrom: {
            type: String
        }
    },
    data: function () {
        return {
            isLoading: false,
            NoteData: [],
            NoRecordForTab: true,
            note: '',
            TotalRecords: 0,
            TotalPages: 0,
            PageSize: 10,
            PageSizeOption: null,
            CurrentPage: 1,
            GroupStart: 1,
            GroupEnd: 3,
            ModelSortBy: '',
            ModelSortExp: '',
            modalDeleteItem: null,
            NoteListType: "card",
            TaskId: null,
            typeNoteTask: '',
            ticketStatus: '',
            disabledAddNote: false,

        }
    },
    watch: {
        notedata: function (value) {
            this.NoteData = value;
            this.TotalRecords = this.numberofrecords;
            this.ProjectId = this.projectid;
           this.ModelSortBy = (this.sortBy == undefined) ? '' : this.sortBy;
            this.ModelSortExp = (this.SortExp == undefined) ? '' : this.SortExp;
            this.NoRecordForTab = (this.NoteData != null && this.NoteData.length > 0) ? false : true;
        }
    },
    created: function () {
        if (this.reqfrom == "dashboard") {
            this.ProjectId = this.$parent.modalNoteTaskItem.PROJECT_ID;
            this.TaskId = this.$parent.modalNoteTaskItem.TASK_ID;
            this.ticketStatus = this.$parent.modalNoteTaskItem.STATUS_NAME;
        }
        else {
            this.ProjectId = this.$parent.modalNoteTaskItem.ProjectId;
            this.TaskId = this.$parent.modalNoteTaskItem.TaskId;

        }
        if (this.ticketStatus == 'Closed' || this.ticketStatus == 'Closed and Locked') {
            this.disabledAddNote = true;
        }
        else {
            this.disabledAddNote = false;
        }
        this.typeNoteTask = this.$parent.typeNoteTask;
        this.FetchNotes();
        //if (this.$parent.modalNoteTaskItem != null && typeof this.$parent.modalNoteTaskItem != 'undefined') {  
        //    this.modal = this.$parent.modalNoteTaskItem;
        //    this.Task = this.$parent.modalNoteTaskItem.TaskId;
        //    this.ProjectId = this.$parent.modalNoteTaskItem.ProjectId;

        //}
    },
    methods: {
        GetPrivilege: function (params) {
            var vm = this;
            DataService.checkPrivilege(params)
                .then(function (response) {
                    if (response.data) {

                    }
                });
        },
        DeleteNote: function (item) {
            if (this.GetPrivilege('controller=Project&action=DeleteNote')) {
                this.modalDeleteItem = item;
                this.DeleteNoteData(this.modalDeleteItem.NOTE_ID);
            }
        },
        DeleteNoteData: function (id) {
            var vm = this;
            if (id > 0) {
                vm.confirmR(this.$t("ConfirmDelete"), this.$t("Delete"), this.$t("Note"), function (result) {
                    if (result) {
                        var postJSON = {
                            ids: id,
                            companyId: vm.$parent.CompanyId,
                            userId: vm.$parent.UserId,
                            moduleName: vm.$parent.ModuleName,
                            subModuleCode: "Project_Detail",
                            refCode: 'TALYGEN_NOTES',
                            refId: vm.$parent.ProjectId
                        };
                        var objectPostString = JSON.stringify(postJSON);
                        DataService.CommonDelete(objectPostString).then(function (response) {
                            if (response.body["status"] == "Success") {
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t("NOTE")]), "success", true, vm.$t("Alert"));
                                vm.FetchNotes();
                                if ($("#hdnpagename").val() == "dashboard") {
                                    vm.$parent.FetchDataKanban('kanban', null);
                                }
                                else if ($("#hdnpagename").val() == "projectToDoList") {
                                    vm.$parent.FetchDataNew('kanban', null);
                                }
                                else {
                                    var urlQueryString = getQueryStringValue('tab');
                                    if (urlQueryString == 'PROJECT_TASKS') {
                                        vm.$parent.TabName('', 'PROJECT_TASKS');
                                    }
                                }
                            }
                            else {
                                vm.ShowAlert(String.Format(vm.$t('DeletedError'), vm.$t('NOTE')), "failure", true, vm.$t("Alert"));
                            }
                            hideLoader();
                        });
                    }
                });
            }
        },
        FetchNotes: function () {
            var vm = this;
            vm.noteData = [];
            vm.PageNumber = (vm.PageNumber == undefined) ? 1 : vm.PageNumber;
            vm.isLoading = true;
            var param = "projectId=" + vm.ProjectId + "&isPublic=" + true + "&taskId=" + vm.TaskId + "&sortBy=" + vm.ModelSortBy + "&sortExp=" + vm.ModelSortExp + "&pageSize=" + vm.PageSize + "&pageNum=" + vm.PageNumber;
            DataService.NoteListingByProjectaAndTask(param).then(function (response) {
                vm.isLoading = false;
                if (response.data.result.data[0] != null && response.data.status == "Success") {
                    vm.NoteData = response.data.result.data;
                    vm.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = (vm.PageNumber == undefined) ? 1 : vm.PageNumber;
                    setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                    vm.NoRecordForTab = false;
                } else {
                    vm.NoRecordForTab = true;
                }
            });
        },
        OpenNote: function () {
            if (this.GetPrivilege('controller=Project&action=AddNoteInProject')) {
                this.$emit("close");
                this.$parent.openProjectNoteModel(null, this.TaskId, this.typeNoteTask)
            }
        },
        OpenEditNote: function (item) {
            if (this.GetPrivilege('controller=Project&action=EditNote')) {
                this.$emit("close");
                this.$parent.openProjectNoteModel(item, this.TaskId, this.typeNoteTask)
            }
        },
        timeFormatOnly: function (value) {
            if (value) {
                return moment(value).format('hh:mm a').toUpperCase();
            }
        }
    },
}
</script>      
