<template>
<transition name="modal">
    <loader :is-visible="isLoading"></loader>
        <div class="my-popups">
            <!-- The Modal -->
            <div class="modal my-popups d-block" id="myModal2">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <!-- Modal Header -->
                        <div class="modal-header">
                            <h4 class="modal-title">{{$t('Update')}} {{$t('Progress')}} - {{TaskName}}</h4>
                            <button type="button" v-on:click="$emit('close')" class="close" data-dismiss="modal"><i class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <!-- Modal body -->
                        <div class="modal-body">
                            <Form ref="ManualValidation">
                                <div class="col-md-12 p-0">
                                    <div class="row">
                                        <div class="col-sm-12">
                                            <label>{{$t('Percentage')}}:</label>
                                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                <div class="slider_setpercentage float-left mt-2">
                                                    <!-- Start slider -->
                                                    <VueSlider ref="slider" v-model="PROGRESS" v-bind="slideroptions" title="Progress bar"></VueSlider>
                                                </div>
                                                <input type="text" v-model="PROGRESS" class="form-control float-right mb-0 slider_setpercentagebox" readonly="">
                                            </div>
                                        </div>
                                        <div class="col-md-12">
                                            <div class="form-group">
                                                <label>{{$t('Comments')}}:<span class="mandatory">*</span></label>
                                                <Field name="ProgressComment" :rules="{ 'required': true }"
                                                label="Progress Comment" v-slot="{ errors,touched,invalid }">
                                                <textarea cols="5" v-model="ProgressComment" v-bind:class="{ 'form-control': true, 'is-invalid': touched && invalid }" placeholder="Comment" />
                                                <span   name="ProgressComment" class="invalid-feedback">{{ errors[0] }}</span>
                                            </Field>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Form>
                        </div>
                        <div class="modal-footer">
                            <a href="javascript:;" class="btn btn-success save-btn" v-on:click="UpdateProgress()"><i class="fa fa-save pr-2"></i><span>{{$t('submit')}}</span></a>
                            <a href="javascript:;" class="btn btn-danger delete-btn" v-on:click="ProgressCancelBtn()"><i class="fa fa-close pr-2"></i><span>{{$t('Cancel')}}</span></a>
                        </div>
                        <div class="col-12">
                            <div class="row">
                                <div class="col-md-12" id="tasklogcommentlistingDiv">
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
  
</template>

<script>
import DataService from '../../../../services/DataService';
export default {
    data: function () {
            return {
                isLoading:false,
                TRACKED_HOURS: '',
                TASK_ID: '',
                PROGRESS: '',
                TaskName: '',
                ProgressComment: '',
                value: 0,
                slideroptions: {
                    dotSize: 14,
                    width: 'auto',
                    height: 4,
                    contained: false,
                    direction: 'ltr',
                    data: null,
                    min: 0,
                    max: 100,
                    interval: 1,
                    disabled: false,
                    clickable: true,
                    duration: 0.5,
                    adsorb: false,
                    lazy: false,
                    tooltip: 'focus',
                    tooltipPlacement: 'top',
                    tooltipFormatter: void 0,
                    useKeyboard: false,
                    enableCross: true,
                    fixed: false,
                    minRange: void 0,
                    maxRange: void 0,
                    order: true,
                    marks: false,
                    dotOptions: void 0,
                    process: true,
                    dotStyle: void 0,
                    railStyle: void 0,
                    processStyle: void 0,
                    tooltipStyle: void 0,
                    stepStyle: void 0,
                    stepActiveStyle: void 0,
                    labelStyle: void 0,
                    labelActiveStyle: void 0,
                },
            }
        },
        created: function () {
            this.TRACKED_HOURS = this.$parent.SelectedItem.TRACKED_HOURS;
            this.TASK_ID = this.$parent.SelectedItem.TASK_ID;
            this.PROGRESS = this.$parent.SelectedItem.PROGRESS;
            this.TaskName = this.$parent.SelectedItem.TASK_NAME;
            // GetTaskComment(this.TASK_ID);

        },        
        methods: {
            ProgressCancelBtn: function () {
                var vm = this;
                vm.StopTimerBtn = false;
                vm.UpdateProgress = false;
                vm.$emit('close');
            },
            UpdateProgress: function () {
                var vm = this;
                vm.$refs.ManualValidation.validate().then((result) => {    
                    if (result.valid) {
                        var obj = {
                            LogID: null,
                            TaskID: vm.TASK_ID,
                            Progress: vm.PROGRESS,
                            Comment: vm.ProgressComment,
                            StatusId: 1001,
                        };
                        var objectPostString = JSON.stringify(obj);
                        DataService.SaveTaskLog(objectPostString).then(function (response) {
                                                    if (response.data != null && response.data != "") {
                                                        if (response.data.Status == "Success") {
                                                            vm.ProgressComment = '';
                                                            vm.StopTimerBtn = false;
                                                            vm.ShowAlert(vm.$t('CreatedSuccessfully',[vm.$t('Progress')]), "success", true, vm.$t("Alert"));
                                                            vm.$emit('close');
                                                            vm.$parent.FetchDataKanban('kanban', null);
                                                        }
                                                        else {
                                                            vm.ShowAlert(vm.$t("Something_Wrong"), 'failure', true, vm.$t('Alert'));
                                                        }
                                                    }
                                                }, function () {
                                                    vm.ShowAlert(vm.$t("Something_Wrong"), 'failure', true, vm.$t('Alert'));
                                                });
                    }
                });

            },

        }
}
</script>