<template>
    <transition name="modal">
        <loader :is-visible="isLoading"></loader>
        <!-- The Modal -->
        <div class="modal my-popups d-block" id="myModal">
            <div class="modal-dialog modal-dialog-centered" style="padding-left:12rem">
                <div class="modal-content mw-800">
                    <!-- Modal Header -->
                    <div class="modal-header">
                        <h4 class="modal-title">{{$t('AddManually')}}</h4>
                        <button type="button" v-on:click="$emit('close')" class="close" data-dismiss="modal"><i
                                class="fa fa-times" aria-hidden="true"></i></button>
                    </div>
                    <!-- Modal body -->
                    <div class="modal-body">
                        <Form ref="ManualValidation">
                            <div class="col-md-12 p-0">
                                <div class="row">
                                    <div class="col-md-6">
                                        <div class="form-group">
                                            <label>{{$t('Date')}}:<span class="text-danger">*</span></label>
                                            <div class="input-group mb-3">
                                                <v-date-picker :popover="popover" :max-date="Dateoptions.maxDate"
                                                    :minute-increment="5" class="input-group" v-model="datepicker">
                                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                        <input v-bind:class="{ 'form-control': true }" :value="inputValue"
                                                            v-on="inputEvents" />
                                                        <div @click="togglePopover()" class="input-group-append"><span
                                                                class="input-group-text"><em
                                                                    class="fa fa-calendar"></em></span></div>
                                                    </template>
                                                </v-date-picker>
                                                <div class="input-group-append">
                                                    <span class="input-group-text datepickerbutton"
                                                        :title="$t('SelectDate')"><i
                                                            class="fa fa-calendar font-16"></i></span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-md-6">
                                        <div class="form-group">
                                            <label for="Billable">{{$t('Billable')}}</label>
                                            <div class="form-group">
                                                <label class="switch">
                                                    <input class="form-check-input dynamic custom-control-input"
                                                        v-model="Billable" type="checkbox" id="Billable" data-change-id="no"
                                                        v-bind:value="Billable" checked="checked" style="display: none;">
                                                    <span class="slider round">
                                                        <span class="slider-yes">{{$t('Yes')}}</span>
                                                        <span class="slider-no">{{$t('No')}}</span>
                                                    </span>
                                                </label>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-md-6" id="divStarttime24">
                                        <div class="form-group">
                                            <label>{{$t('StartTime')}}:<span class="text-danger">*</span></label>
                                            <div class="input-group mb-3">
                                                <Field name="txtStartTime" :rules="{ 'required': true }" label="Start Time"
                                                    v-slot="{ errors, changed }">
                                                    <v-date-picker :popover="popover"
                                                        :max-date="Dateoptions.maxDate" :minute-increment="5"
                                                        class="input-group" v-model="txtStartTime"
                                                        @dayclick="AddStartTimeBlur(AddManuallyModalItem)"
                                                        @update:modelValue="changed">
                                                        <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                            <input
                                                                v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                :value="inputValue" v-on="inputEvents"
                                                                placeholder="HH:MM" />
                                                            <div @click="togglePopover()" class="input-group-append"><span
                                                                    class="input-group-text">
                                                                    <em class="fa fa-calendar"></em></span></div>
                                                        </template>
                                                    </v-date-picker>
                                                    <span   name="txtStartTime"
                                                        class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                </Field>
                                            </div>
                                            <select id="ddlStartAM" v-model="ddlStartAM" class="time-am-pm d-none">
                                                <option value="AM">{{$t('AM')}}</option>
                                                <option value="PM">{{$t('PM')}}</option>
                                            </select>
                                        </div>
                                    </div>
                                    <div class="col-md-6" id="divStoptime24">
                                        <div class="form-group">
                                            <label>{{$t('StopTime')}}:<span class="text-danger">*</span></label>
                                            <div class="input-group mb-3">
                                                <Field name="txtStopTime" :rules="{ 'required': true }" label="Stop Time"
                                                    v-slot="{ errors, changed }">
                                                    <v-date-picker :popover="popover"
                                                        :max-date="Dateoptions.maxDate" :minute-increment="5"
                                                        class="input-group" v-model="txtStopTime"
                                                        @dayclick="AddEndTimeBlur(AddManuallyModalItem)"
                                                        @update:modelValue="changed">
                                                        <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                            <input
                                                                v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                :value="inputValue" v-on="inputEvents"
                                                                placeholder="HH:MM" />
                                                            <div @click="togglePopover()" class="input-group-append"><span
                                                                    class="input-group-text">
                                                                    <em class="fa fa-calendar"></em></span></div>
                                                        </template>
                                                    </v-date-picker>
                                                    <span   name="txtStopTime"
                                                        class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                </Field>
                                            </div>
                                            <select id="ddlStopAM" v-model="ddlStopAM" class="time-am-pm d-none">
                                                <option value="AM">{{$t('AM')}}</option>
                                                <option value="PM">{{$t('PM')}}</option>
                                            </select>

                                        </div>
                                    </div>

                                    <div class="col-md-6" id="divStarttime24" style="display: none;">
                                        <div class="form-group">
                                            <label>{{$t('StartTime')}}:<span class="text-danger">*</span></label>
                                            <div class="input-group mb-3">
                                                <v-date-picker :popover="popover" :max-date="Dateoptions.maxDate"
                                                    :minute-increment="5" class="input-group" v-model="txtStartTime24"
                                                    @dayclick="AddEndTimeBlur(AddManuallyModalItem)">
                                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                        <input v-bind:class="{ 'timedate timedate_hs': true }"
                                                            placeholder="HH:MM" :value="inputValue" v-on="inputEvents" />
                                                        <div @click="togglePopover()" class="input-group-append"><span
                                                                class="input-group-text"><em
                                                                    class="fa fa-calendar"></em></span></div>
                                                    </template>
                                                </v-date-picker>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-md-6" id="divStoptime24" style="display: none;">
                                        <div class="form-group">
                                            <label>{{$t('StopTime')}}:<span class="text-danger">*</span></label>
                                            <div class="input-group mb-3">
                                                <v-date-picker name="txtStopTime" :popover="popover"
                                                    :max-date="Dateoptions.maxDate" :minute-increment="5"
                                                    class="input-group" v-model="txtStopTime24"
                                                    @dayclick="AddEndTimeBlur(AddManuallyModalItem)">
                                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                        <input v-bind:class="{ 'timedate timedate_hs': true }"
                                                            placeholder="HH:MM" :value="inputValue" v-on="inputEvents" />
                                                        <div @click="togglePopover()" class="input-group-append"><span
                                                                class="input-group-text">
                                                                <em class="fa fa-calendar"></em></span></div>
                                                    </template>
                                                </v-date-picker>
                                            </div>
                                        </div>
                                    </div>
                                  <div class="col-md-6 d-none">
                                        <div class="form-group">
                                            <label for="Monday">{{$t('HourFormat')}}</label>
                                            <div class="form-group">
                                                <label class="switch">
                                                    <input class="form-check-input custom-control-input" type="checkbox"
                                                        id="Monday" data-change-id="no" value="option2"
                                                        style="display: none;">
                                                    <span class="slider round"><span>Yes</span></span>
                                                </label>
                                            </div>
                                        </div>
                                    </div>

                                    <div class="col-md-12">
                                        <div class="form-group">
                                            <label for="FormControlTextarea1">{{$t('Description')}}:<span
                                                    class="text-danger">*</span></label>
                                            <Field name="FormControlTextarea1" :rules="{ 'required': true }"
                                                label="Description" v-slot="{ errors,touched,invalid }">
                                                <textarea cols="5" v-model="DescriptionManualy"
                                                    v-bind:class="{ 'form-control': true, 'is-invalid': touched && invalid }"
                                                    placeholder="Description" />
                                                <span   name="FormControlTextarea1"
                                                    class="invalid-feedback">{{ errors[0] }}</span>
                                            </Field>
                                        </div>
                                    </div>
                                </div>
                                <div class="row flex-row-reverse">
                                    <div class="col-lg-6 text-right">
                                        <a href="javascript:;" v-on:click="SaveManually()"
                                            class="btn btn-success save-btn mr-2" :title="$t('Save')"><i
                                                class="fa fa-floppy-o pr-2" aria-hidden="true"></i><span>{{$t('Save')}}
                                            </span></a>
                                        <a href="javascript:;" v-on:click="MannualDivCancel()"
                                            class="btn btn-danger delete-btn" :title="$t('Cancel')">
                                            <i class="fa fa-times pr-2"
                                                aria-hidden="true"></i><span>{{$t('Cancel')}}</span>
                                        </a>
                                    </div>
                                    <div class="col-lg-6">
                                        <!-- <div class="py-2">@HtmlHelpers.MandatoryStringFull("R")</div> -->
                                    </div>
                                </div>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        </div>

    </transition>
    
</template>

<script>
import DataService from '../../../../services/DataService';

export default {
    props:{
        CompanyId:null,
        UserId:null
    },
    data() {
        return {
            isLoading:false,
            TotalTime: '',
            TotalTimeText: '',
            txtStartTime: '',
            txtStopTime: '',
            txtStartTime24: '',
            txtStopTime24: '',
            ddlStartAM: '',
            ddlStopAM: '',
            datepicker: '',
            Billable: '',
            DescriptionManualy: '',
            secondsPerMinute: 60,
            minutesPerHour: 60,
            AddManuallyModalItem: null,
            PROJECT_ID: '',
            TASK_ID: '',
            Timeoptions: {
                format: String('h:mm tt').replace("t", "A").replace("t", ""),
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                widgetPositioning: {
                    horizontal: 'left',
                    vertical: 'bottom'
                }
            },
            Dateoptions: {
                format: String('M/dd/yyyy').toUpperCase(),
                useCurrent: true,
                showClear: true,
                showClose: true,
                maxDate: new Date(),
                widgetPositioning: {
                    horizontal: 'left',
                    vertical: 'bottom'
                }
            },
        }
    },
    created: function () {
        this.PROJECT_ID = this.$parent.SelectedItem.PROJECT_ID;
        this.TASK_ID = this.$parent.SelectedItem.TASK_ID;
    },
    methods: {
        CreatedFunction: function () {
            var vm = this;
            vm.time();
            var date = new Date(10);
            if (vm.ToDoListData != null) {
                //vm.result = JSON.parse(vm.toDoList).DATA;
                $.each(vm.ToDoListData, function (index, item) {
                    var category = item.TASKS;
                    var grepResultCheck = $.grep(vm.GroupResult, function (element, index) {
                        return element.Category == category;
                    });
                    if (grepResultCheck.length == 0) {
                        var grepResult = $.grep(vm.result, function (element, index) {
                            element.MannualyBtn = false;
                            element.AutomaticBtn = false;
                            element.StopTimerBtn = false;
                            element.UpdateProgress = false;
                            element.RequestTimeBtn = false;
                            element.ClientTeam = [];
                            element.ClientTeamValue = [];
                            element.PMTeam = [];
                            element.PMValue = [];
                            element.UserList = '';
                            element.ProgressComment = '';
                            element.RequestId = '';
                            element.Hours = '';
                            element.Minutes = '';
                            element.RequestTimeSec = 0,
                                element.sDate = '';
                            element.ddlStartAM = '';
                            element.txtStartTime24 = '';
                            element.datepicker = vm.CurrentDate('date', vm.Timer);
                            element.chkHourFormat = false;
                            element.eDate = '';
                            element.ddlStopAM = '';
                            element.TotalTime = '00:00:00';
                            element.TotalTimeText = '00:00:00';
                            element.DescriptionManualy = '';
                            element.AddManualy = false;
                            return element.TASKS == category;
                        });
                        if (grepResult.length > 0) {
                            var GroupArray = {
                                Category: category,
                                Items: grepResult
                            }
                            vm.GroupResult.push(GroupArray);
                        }
                    }
                });
                if (vm.ToDoListData.length > 0) {
                    vm.noRecordToDoList = false;
                }
                else {
                    vm.noRecordToDoList = true;
                }
            }
            else {
                vm.noRecordToDoList = true;
            }
            setTimeout("CheckBoxBootstrap()", 100);
            setTimeout("ShowfileUploadWithCulture();", 550);
        },
        SaveManually: function () {
            var vm = this;            
            vm.$refs.ManualValidation.validate().then((result) => {    
                if (result.valid) {
                    var START_TIME = vm.txtStartTime;
                    var selectedDate = '';
                    if (vm.datepicker == '') {
                        selectedDate = new Date();
                    }
                    else {
                        selectedDate = new Date(vm.datepicker);
                    }
                    var dd = selectedDate.getDate(); //yields day
                    var MM = selectedDate.getMonth(); //yields month
                    var yyyy = selectedDate.getFullYear(); //yields year
                    var StartDate = yyyy + "-" + (MM + 1) + "-" + dd;
                    var STOP_TIME = vm.txtStopTime;
                    var COMPANY_ID = vm.CompanyId;
                    var CREATED_BY = vm.UserId;
                    var PROJECT_ID = vm.PROJECT_ID;
                    var TASK_ID = vm.TASK_ID;
                    var IS_BILLABLE = 0;
                    if (vm.Billable == true) {
                        IS_BILLABLE = 1;
                    }
                    var param={
                            trackDescription: vm.DescriptionManualy,
                            startTime: START_TIME,
                            startDate: StartDate,
                            stopTime: STOP_TIME,
                            isBillable: IS_BILLABLE,
                            companyId: COMPANY_ID,
                            createdBy: CREATED_BY,
                            totalTime: vm.TotalTimeText,
                            projectId: PROJECT_ID,
                            taskId: TASK_ID,
                            TagIds: ''
                        };
                        vm.isLoading=true;
                    DataService.TimerAddManually(param).then(function(result){
                        vm.isLoading=false;
var response=result.data;
if (response.status == "-3") {
                                vm.ShowAlert(vm.$t("TimeRangeValidation"), "warning", true, vm.$t("Alert"));
                            }
                            else if (response.Status == "-4") {
                                vm.ShowAlert(vm.$t("TimerStartDateOverflow"), "warning", true, vm.$t("Alert"));
                            }
                            else if (response.Status == "-2") {
                                vm.ShowAlert(vm.$t("PleaseTryAgain"), "failure", true, vm.$t("Alert"));
                            }
                            else if (response.Status == "-1") {
                                vm.ShowAlert(vm.$t("PleaseTryAgain"), "failure", true, vm.$t("Alert"));
                            }
                            else if (response.Status == "Saved") {

                                vm.TotalTimeText = '00:00:00';
                                //$('#TotalTime').countdown('destroy');
                                vm.sDate = '';
                                vm.eDate = '';
                                vm.DescriptionManualy = '';
                                vm.IS_BILLABLE = false;
                                //$('#chkBillable').attr("checked", false);
                                //$('#hdnTRACK_ID').val('0');
                                //$('#hdnLINKED_TRACK_ID').val('0');
                                vm.datepicker = vm.CurrentDate('date', vm.Timer);
                                if (response.count == 0) {
                                    vm.ShowAlert(vm.$t("TimeEntrySavedSuccessfully"), "success", true, vm.$t("Alert"));
                                    vm.$emit('close');
                                    vm.$parent.FetchDataKanban('kanban', null);
                                }
                                else if (response.count == 1) {
                                    vm.ShowAlert(vm.$t("TimeEntryRequestSentSuccessfully"), "success", true, vm.$t("Alert"));
                                    vm.$emit('close');
                                    vm.$parent.FetchDataKanban('kanban', null);
                                }
                                else if (response.count == 2) {
                                    vm.ShowAlert(vm.$t("TimeEntryAutoApprovedSuccess"), "success", true, vm.$t("Alert"));
                                }
                                vm.$emit('close')
                                vm.MannualDiv = false;
                                vm.AddManualy = false;
                            }
                            else if (response.Status == "overlap") {

                                var dialog = $('#alert');
                                dialog.html('<div class="grapghloader">&nbsp; </div>');
                                dialog.html(response.viewString);
                                dialog.dialog('open');
                            }
                    })                   
                    
                }
            });

        },
        CurrentDate: function (format, userDate) {
            var newDate = new Date(userDate);
            if (format == 'date') {
                var month = newDate.getMonth() + 1;
                var year = newDate.getFullYear();
                var day = newDate.getDate();
                var stringDate = "";
                if (month < 10) {
                    month = '0' + month;
                }
                stringDate = this.$options.filters.formatDate(newDate);
                return stringDate;
            }
            if (format == 'time') {
                return this.$options.filters.formatDateTime(newDate);//Globalize.format(newDate, 'M/dd/yyyy' + " " + 'h:mm A');
            }
            return newDate;
        },
        CheckIsValidTime: function (timeStr) {

            // Checks if time is in HH:MM:SS AM/PM format.
            // The seconds and AM/PM are optional.
            var vm=this;
            var status = 1;
            var timePat = /^(\d{1,2}):(\d{2})(:(\d{2}))?(\s?(AM|am|PM|pm|Am|Pm|aM|pM))?$/;
            var matchArray = timeStr.match(timePat);
            if (matchArray == null) {
                vm.ShowAlert(vm.$t('TimeNotValidFormat'), 'warning', true, vm.$t('Alert'));
                //talygen.alert(vm.$t("TimeNotValidFormat"));
                status = 0;
                return status;
            }
            hour = matchArray[1];
            minute = matchArray[2];
            second = "00";//matchArray[4];
            ampm = matchArray[6];
            if (second == "") { second = null; }
            if (ampm == "") { ampm = null }
            if (($('#chkHourFormat').attr('checked') == "checked")) {
                if (hour < 0 || hour > 23) {
                    vm.ShowAlert(vm.$t('NotValidTime24'), 'warning', true, vm.$t('Alert'));
                    // talygen.alert(vm.$t("NotValidTime24"));
                    status = 0;
                    return status;
                }
            }
            else {
                if (hour < 0 || hour > 12) {
                    vm.ShowAlert(vm.$t('HourLimits'), 'warning', true, vm.$t('Alert'));
                    //talygen.alert(vm.$t("HourLimits"));
                    status = 0;
                    return status;
                }
            }
            if (minute < 0 || minute > 59) {
                vm.ShowAlert(vm.$t('MinutesLimits'), 'warning', true, vm.$t('Alert'));
                // talygen.alert(vm.$t("MinutesLimits"));
                status = 0;
                return status;
            }
            if (second != null && (second < 0 || second > 59)) {
                vm.ShowAlert(vm.$t('SecondsLimits'), 'warning', true, vm.$t('Alert'));
                //talygen.alert(vm.$t("SecondsLimits"));
                status = 0;
                return status;
            }
            return status;
        },
        GetDifference: function (item, type) {
            var vm = this;
            var txtStart = vm.txtStartTime == "HH:MM:SS" ? '' : vm.txtStartTime;//+ " " + amStart.val();
            var txtEnd = vm.txtStopTime == "HH:MM:SS" ? '' : vm.txtStopTime;//+ " " + amStop.val();
            if (txtStart == " AM" || txtStart == " PM" || txtStart == "__:__:__ AM" || txtStart == "__:__:__ PM")
                txtStart = "";
            if (txtEnd == " AM" || txtEnd == " PM" || txtEnd == "__:__:__ AM" || txtEnd == "__:__:__ PM")
                txtEnd = "";

            if (!testAttribute("input", "placeholder")) {
                if (txtStart == "HH:MM:SS" || txtStart == "__:__:__")
                    txtStart = "";
                if (txtEnd == "HH:MM:SS" || txtEnd == "__:__:__")
                    txtEnd = "";
                if (txtStart == "" || txtStart == "__:__:__")
                    vm.txtStartTime = "HH:MM:SS";
                if (txtEnd == "" || txtEnd == "__:__:__")
                    vm.txtStopTime = "HH:MM:SS";
            }
            var status = false;
            var starts = txtStart.replace(" ", "");
            var ends = txtEnd.replace(" ", "");
            var starttime = txtStart.replace(" ", "");
            var endtime = txtEnd.replace(" ", "");
            if (starttime == "" && endtime == "") {
                starttime = getcurrenttime();
                endtime = getcurrenttime();
            }
            if (starttime == "" || endtime == "") {
                return status;
            }
            var startstatus = this.CheckIsValidTime(starttime);
            if (startstatus == 0) {
                txtStartTimer.val('');
                return status;
            }
            var endstatus = this.CheckIsValidTime(endtime);
            if (endstatus == 0) {
                txtEndTimer.val('');
                return status;
            }

            var startAmPm = starts.substr(starts.length - 2, starts.length).toUpperCase();
            var newStart = "";
            var newEnd = "";
            if (startAmPm != "AM" && startAmPm != "PM") {
                startAmPm = "AM";
                newStart = starttime;
            }
            else {
                newStart = starttime.substr(0, starttime.length - 2);
            }
            var endAmPm = ends.substr(ends.length - 2, ends.length).toUpperCase();
            if (endAmPm != "AM" && endAmPm != "PM") {
                endAmPm = "AM";
                newEnd = endtime;
            }
            else {
                newEnd = endtime.substr(0, endtime.length - 2);
            }
            if (startAmPm == "PM" || startAmPm == "pm") {
                if (newStart != '') {
                    var start1 = newStart.split(':');
                    var hr = start1[0].toString();
                    if (hr != 12) {
                        hr = (parseInt(hr, 10) + 12).toString();
                        newStart = hr + ":" + start1[1].toString();
                    }
                    if (start1.length >= 3)
                        newStart += ":" + start1[2].toString();
                }
            }
            if (endAmPm == "PM" || endAmPm == "pm") {
                if (newEnd != '') {
                    var end1 = newEnd.split(':');
                    var hr = end1[0].toString();
                    if (hr != 12) {
                        hr = (parseInt(hr, 10) + 12).toString();
                        newEnd = hr + ":" + end1[1].toString();
                    }
                    if (end1.length >= 3)
                        newEnd += ":" + end1[2].toString();
                }
            }
            if (startAmPm == "AM" || startAmPm == "am") {
                var hr = starttime.substr(0, 2);
                if (hr == 12) {
                    hr = "00";
                    newStart = hr + ":" + newStart.substr(3, newStart.length);
                }
            }
            if (endAmPm == "AM" || endAmPm == "am") {
                var hr = endtime.substr(0, 2);
                if (hr == 12) {
                    hr = "00";
                    newEnd = hr + ":" + newEnd.substr(3, newEnd.length);
                }
            }
            var time1 = vm.HMStoSec1(newStart);
            var time2 = vm.HMStoSec1(newEnd);
            var diff = 0;
            if (startAmPm == endAmPm) {
                if (time1 == time2) {
                    time2 = time2;
                }
                else if (time1 > time2) { time2 = time2 + 86400; }
            }
            if (startAmPm != endAmPm) {
                if (startAmPm == "AM" && endAmPm == "PM") {
                    if (time1 > time2) {
                        time2 = time2 - 86400;
                    }
                    else {
                    }
                }
                if (startAmPm == "PM" && endAmPm == "AM") {
                    if (time1 > time2) {
                        time1 = time1 - 86400;
                    }
                    else {
                        time2 = time2 + 86400;
                    }
                }
            }
            if (time2 > time1) {
                diff = time2 - time1;
            }
            if (time1 > time2) {
                diff = (time1 - time2);
            }
            if (type == "save") {

                var textStop = txtStopTime;
                if (textStop != '') {
                    $('#TotalTime').html(this.convertSecondsToHHMMSSNewManual(diff));
                    vm.TotalTimeText = this.convertSecondsToHHMMSSNewManual(diff);
                }
            }

            status = true;
            return status;

        },
        convertSecondsToHHMMSSNewManual: function (intSecondsToConvert) {

            var hours = this.convertHours(intSecondsToConvert);
            var minutes = this.getRemainingMinutes(intSecondsToConvert);
            minutes = minutes == 60 ? "00" : minutes;
            var seconds = this.getRemainingSeconds(intSecondsToConvert);
            if (hours < 10) hours = "0" + hours;
            if (minutes < 10) minutes = "0" + minutes;
            if (seconds < 10) seconds = "0" + seconds;
            return hours + ":" + minutes + ":" + seconds
        },
        convertMinutes: function (intSeconds) {
            return Math.floor(intSeconds / this.secondsPerMinute)
        },
        getRemainingMinutes: function (intSeconds) {
            var intTotalMinutes = this.convertMinutes(intSeconds);
            return intTotalMinutes % this.minutesPerHour
        },
        getRemainingSeconds: function (intTotalSeconds) {
            return intTotalSeconds % this.secondsPerMinute
        },
        convertSecondsToHHMMSSNew: function (intSecondsToConvert) {
            var hours = this.convertHours(intSecondsToConvert);
            var minutes = this.getRemainingMinutes(intSecondsToConvert);
            minutes = minutes == 60 ? "00" : minutes;
            var seconds = this.getRemainingSeconds(intSecondsToConvert);
            if (hours < 10) hours = "0" + hours;
            if (minutes < 10) minutes = "0" + minutes;
            if (seconds < 10) seconds = "0" + seconds;
            return hours + " hrs " + minutes + " min " + seconds + " sec"
        },
        convertHours: function (intSeconds) {

            var minutes = this.convertMinutes(intSeconds);
            var hours = Math.floor(minutes / this.minutesPerHour);
            return hours
        },
        convertHoursTwoDecimal: function (intSeconds) {

            var minutes = this.convertMinutes(intSeconds);
            var hours = (minutes / this.minutesPerHour).toFixed(2);
            return hours
        },
        convertMinutes: function (intSeconds) {

            return Math.floor(intSeconds / this.secondsPerMinute)
        },
        HMStoSec1: function (T) {
            var A = T.split(/\D+/);
            if (A.length == 2) {
                return ((A[0] * 60 + +A[1]) * 60);
            }
            else {
                return ((A[0] * 60 + +A[1]) * 60 + +A[2]);
            }
        },
        AddStartTimeBlur: function (item) {

            var vm = this;
            var sDate = vm.txtStartTime;
            //var eDate= this.eDate;
            if (sDate.length > 0) {

                var st = this.CheckIsValidTime(sDate);
                if (st == 1) {
                    var startTime = vm.txtStartTime;
                    var starthours = startTime.substr(0, 2);
                    if (vm.ddlStartAM.toLowerCase() == 'pm') {
                        starthours = parseInt(starthours) + parseInt(12);
                        startTime = starthours + ":" + startTime.substr(3, startTime.length);
                    }
                    vm.txtStartTime24 = startTime;
                    // 12 hour format to 24 hour format
                    var stopTime = vm.txtStopTime;
                    var stophours = stopTime.substr(0, 2);
                    if (vm.ddlStartAM.toLowerCase() == 'pm') {
                        stophours = parseInt(stophours) + parseInt(12);
                        stopTime = stophours + ":" + stopTime.substr(3, stopTime.length);
                    }
                    vm.txtStopTime24 = stopTime;
                    this.GetDifference(item, 'save');
                    return true;
                }
            }
        },
        AddEndTimeBlur: function (item) {
            var vm = this;
            var eDate = vm.txtStopTime;
            //var eDate= this.eDate;
            if (eDate.length > 0) {
                var st = this.CheckIsValidTime(eDate);
                if (st == 1) {
                    var startTime = vm.txtStartTime;
                    var starthours = startTime.substr(0, 2);
                    if (vm.ddlStartAM.toLowerCase() == 'pm') {
                        starthours = parseInt(starthours) + parseInt(12);
                        startTime = starthours + ":" + startTime.substr(3, startTime.length);
                    }
                    vm.txtStartTime24 = startTime;
                    // 12 hour format to 24 hour format
                    var stopTime = vm.txtStopTime;
                    var stophours = stopTime.substr(0, 2);
                    if (vm.ddlStartAM.toLowerCase() == 'pm') {
                        stophours = parseInt(stophours) + parseInt(12);
                        stopTime = stophours + ":" + stopTime.substr(3, stopTime.length);
                    }
                    vm.txtStopTime24 = stopTime;
                    this.GetDifference(item, 'save');
                    return true;
                }
            }
        },
        MannualDiv: function (item) {           
            this.showAddManuallyModal = true;
            var vm = this;
            var selectedDate = vm.CurrentDate('date', vm.Timer);
            DataService.GetApprovalStatusForWeekly('date='+selectedDate).then(function (response) {
                if (response.val == 0) {
                    $('#hdnStatusCode').val(1);
                }
            });
            if ($('#hdnStatusCode').val() == "0") {
                vm.ShowAlert(vm.$t('EtreesApprovedpending'), 'warning', true, vm.$t('Alert'));
                $('#hdnStatusCode').val(0);
                return false;
            }
            else {
                vm.AddManualy = true;
                vm.RequestTimeBtn = false
                vm.StopTimerBtn = false;
                vm.MannualyBtn = false;
                vm.AutomaticBtn = false;
            }
       
        },
        MannualDivCancel: function () {
            var vm = this;
            vm.RequestTimeBtn = false
            vm.StopTimerBtn = false;
            vm.MannualyBtn = false;
            vm.AutomaticBtn = false;
            vm.$emit('close');
        },
    }
}
</script>