<template>
<div id="divdashboardwidgetdata" v-cloak>
    <div class="heading-border mt-2 mb-2 " id="teamv" v-if="showTeam">
        <h5 class="h5"><span>Team View</span></h5>
        <div class="input-group float-right p-0 teamv taskclass">
            <div class="form-control p-0" style="z-index:999">
                <treeselect   :multiple="true"
                            :options="options"
                            placeholder="Select your team(s)"
                            v-model="value"
                            value-consists-of="LEAF_PRIORITY"
                            :limit="1"
                            append-to-body="appendToBody" />
                <!-- <treeselect-value value="value" />  -->
            </div>
            <div class="input-group-append" style="min-width:75px;">
                <span class="input-group-text p-0 border-0 w-100">
                    <a v-on:click="searchFunction()" href="javascript:;" title="@Resources.Search" class="btn-success float-left mr-1"><i class="fa fa-search"></i></a>
                    <a v-on:click="resetFunction()" href="javascript:;" title="" class="btn btn-danger clearsearchtext float-right m-0 w-auto" data-toggle-tooltip="tooltip" data-original-title="Clear"><i class="fa fa-refresh"></i></a>
                </span>
            </div>


        </div>
    </div>
    <ul class="dashbord-widget">

        <li v-bind:class="ReturnClass(index)" v-for="(item,index) in WidgetData" :key="index">
            <a v-bind:href="item.ActionLink=='' ? 'javascript:;': item.ActionLink">
                <h3>{{item.WidgetCount}}{{$t(item.Legend)}}<br><span>{{$t(item.WidgetName)}}</span></h3>
                <i v-bind:class="'fa '+ item.WidgetIcon" aria-hidden="true"></i>
            </a>
        </li>
    </ul>
</div>
</template>

<script>
import DataService from '../../../../services/DataService';
import Treeselect from '@riophae/vue-treeselect';
import '@riophae/vue-treeselect/dist/vue-treeselect.css';
export default {
    components: {'treeselect': Treeselect },
    props:{
        moduleName:null,
        checkVueodolist:null,
    },   
    data: function () {
            return {         
                isLoading:true,       
                ApiURLCount: "subModuleCode=Project_Detail&moduleName=",
                WidgetData:[],
                value: [],
                options: [],
                users: null,
                showTeam:false
            }
        },
        
        created: function () {

            this.ApiURLCount=this.ApiURLCount+this.moduleName;
            this.FetchData('kanban', null);
        },
        methods: {
            FetchData: function (type, userId) {
                
                var vm = this;
                vm.isLoading=true;
                var params =this.ApiURLCount+ "&pageSize=100&pageNumber=1&sortBy=&sortExp=&searchCondition=&projectId=&type=" + type + "&userIds=" + userId;               
                DataService.DashBoardMinWidgetData(params).then(function (response) {
                    
                    vm.isLoading=false;
                        var responseData = response.data.result;
                        if (response.data != null) {
                            if (typeof responseData.data != "undefined") {
                                vm.WidgetData = responseData.data;
                                vm.options = responseData.team;
                            }
                            else {
                                vm.WidgetData = [];
                                vm.options = [];
                            }
                        }
                        else {
                            vm.WidgetData = [];
                            vm.options = [];
                        }
                    });
            },
            searchFunction: function () {
                var vm = this;
                vm.users = vm.value.join(', ');
                this.FetchData('kanban', vm.users);
                checkVueodolist.FetchDataKanban('kanban', vm.users);
                loadReports();
                checkVueodolist.refreshProjectDropDown();
            },
            resetFunction: function () {
                var vm = this;
                 vm.value='';
                vm.users = '';
                this.FetchData('kanban', '');
                checkVueodolist.FetchDataKanban('kanban','');
                loadReports();
                checkVueodolist.refreshProjectDropDown();
            },
            ReturnClass: function (index) {
                var className = "";
                switch (index) {
                    case 0:
                        className = "one_wid";
                        break;
                    case 1:
                        className = "two_wid";
                        break;
                    case 2:
                        className = "three_wid";
                        break;
                    case 3:
                        className = "four_wid";
                        break;
                    case 4:
                        className = "five_wid";
                        break;
                    default:
                        className = "one_wid";
                }
                return className;
            }            
        }
}
</script>
<style>
.vue-treeselect__placeholder, .vue-treeselect__single-value {
    line-height: 13px;
}
</style>