<template>
    <div>
        <loader :is-visible="isLoading"></loader>
  <section class="page-action">
    <div class="container-fluid px-0">
        <div class="row">
            <input type="hidden" value="@titleName" id="hdnModuleName" />
            <div class="col-md-12 padding-t_8">
                <div class="theme-primary partition partition-full p-actions-expand before-d-none overflow-visible">
                    <span class="p-name text-white">{{(titleName + " " +$t('dashboard'))}}</span>
                    <span class="p-actions  float-right">
                        <a href="javascript:;" class="p-action-btn text-white link" @click="MyViews()"><i class="fa fa-user" aria-hidden="true"></i><br> {{$t('My')}} {{$t('View')}}</a>
                        <a href="javascript:;" class="p-action-btn text-white" id="teamview" @click="TeamView()"><i class="fa fa-users" aria-hidden="true"></i><br>{{$t('Team')}} {{$t('View')}}</a>
                        <a class="p-action-btn text-white d-none" id="ancChangeView" data-isshow="0"><i class="fa fa-pencil" aria-hidden="true"></i><br>Change View</a>
                        <div :class="{'dropdown p-action-btn text-white':true,'show':MenuButton}" id="ddlChangeView">
                            <a class="text-white dropdown-toggle d-inline-block" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" 
                            aria-expanded="false" @click="showDropDowMenu()">
                                <i class="fa fa-eye" aria-hidden="true"></i><br />
                                <span class="spnChangeView">{{$t('Select')}} {{$t('View')}}</span>
                            </a>
                            <div :class="{'dropdown-menu':true,'show':MenuButton}" :style="{'position: absolute; transform: translate3d(1882px, 45px, 0px); top: 0px; left: 0px; will-change: transform;':MenuButton}" aria-labelledby="dropdownMenuButton">
                                <a class="dropdown-item" data-value="B" href="javascript:;">{{$t('ALL')}}</a>
                                <a class="dropdown-item" data-value="R" href="javascript:;">{{$t('Reports')}}</a>
                                <a class="dropdown-item" data-value="T" href="javascript:;">{{$t('ToDoList')}}</a>
                            </div>
                        </div>
                    </span>
                    <span class="collapse-head-btns">
                        <a href="javascript:;" class="collapse-btns"><i class="fa fa-angle-up text-white"></i></a>
                    </span>
                </div>
            </div>
        </div>
    </div>
    <input type="hidden" value="dashboard" id="hdnpagename" />
</section>
<section class="main-content nd-combo-view">
    <div id="parentPageDiv" class="container-fluid padding-t_10 px-0">
        <div class="col-md-12 p-0">
            <!--dashboard-->
          <CountList :moduleName="moduleName" ref="countList"></CountList>
            <!--dashboard-->
            <div class="clearfix"></div>
            <div class="col-lg-12 p-0" id="divToDoListSort">
                <div class="row mt-2">
                    <div class="col-md-5" id="divToDoList">
                        <div class="heading-border" style="z-index:9;">
                            <h5 class="h5"><span>{{(titleName + " " + $t('ToDoList'))}}</span></h5>
                        </div>
                        <div id="toDoListing">
                            <ToDoList v-if="encCompanyId" :moduleName="moduleName"  :encCompanyId="encCompanyId" :encUserId="encUserId" :encTrackId="encTrackId"
                            @fetch-count-list="FetchCountListData" ref="todolist"></ToDoList>
                        </div>
                    </div>
                    <div class="col-md-7 mt-4 mt-lg-0" id="divReportsdata">
                        <div class="heading-border">
                            <h5 class="h5"><span>{{(titleName + " " + $t('Reports'))}}</span></h5>
                        </div>
                        <div class="nd-repoort-filter">
                            <span class="p-actions float-right">
                            <date-range pgaction="global" @set-date="SetDateRange"></date-range>                              
                            </span>
                        </div>
                        <div class="dashbord-graph-widget row bg-lgray border m-0 py-3 with-combo scrollbarmini" id="Reports">                       
                            <WidgetReport v-if="widgets && widgets.length>0 && fromDate" ref="widget" :widgets="widgets" :moduleName="moduleName" :viewType="viewType"
                                :fromDate="fromDate" :toDate="toDate" @reload-report="GetReports"></WidgetReport>
                        </div>
                        <div class="clearfix"></div>
                    </div>
                    <div class="col-md-12 mt-3" id="divAddMoreReports">
                        <div class="text-right mt-1">
                            <a href="javascript:;" id="hlkAddMoreReports" class="btn btn-success" @click="OnAddMore">
                                <i class="fa fa-plus pr-2"></i>{{$t('AddMore')}}</a>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-xl-12 p-0" style="display:none;" id="divToDoListMain">    
            </div>
        </div>   
    </div>
   
    <div class="my-popups view-popup">
        <!-- The Modal -->
        <div class="modal fade" id="myModal">
            <div class="modal-dialog modal-lg modal-dialog-centered">
                <div class="modal-content">
                    <!-- Modal Header -->
                    <div class="modal-header">
                        <h4 class="modal-title">Change View</h4>
                        <button type="button" class="close" data-dismiss="modal"><i class="fa fa-times" aria-hidden="true"></i></button>
                    </div>
                    <!-- Modal body -->
                    <div class="modal-body p-0">
                        <div class="row m-0">
                            <div class="col-sm-12 col-md-12 col-lg-7 col-xl-7 float-left d-none">
                                <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pr-1 pl-0">
                                    <div class="form-group m-0">
                                        <input id="search" class="form-control" type="text" placeholder="Search" autocomplete="off">
                                    </div>
                                </div>
                                <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pl-0">
                                    <div class="search-btm-btn background-0 text-left form-check-inline">
                                        <a title="" id="Go" href="javascript:;" class="btn btn-success mr-1 px-4" data-toggle-tooltip="tooltip" data-original-title="Search"><i class="fa fa-search"></i></a>
                                        <a href="javascript:;" title="" class="btn btn-danger clearsearchtext px-4" data-toggle-tooltip="tooltip" data-original-title="Clear"><i class="fa fa-refresh"></i></a>
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-5 col-xl-5 float-left d-none">
                                <div class="float-right">
                                    <a href="javascript:;" class="btn btn-primary"> <i class="fa fa-plus pr-2" aria-hidden="true"></i>Add</a>
                                </div>
                            </div>
                        </div>
                        <ul class="shortcuts-menu pb-3 ">
                            <li class="">
                                <a href="/Project/ToDoList?&projectId=&type=kanban" target="_blank" class="short-menu-item"><span class="left-icon-m"><i class="fa fa-th" aria-hidden="true"></i></span> <span class="short-item-name">Kanban View</span></a>
                            </li>
                            <li class="d-none">
                                <a href="New-dashboard-combo-view.html" class="short-menu-item"><span class="left-icon-m"><i class="fa fa-users" aria-hidden="true"></i></span> <span class="short-item-name">Show By Employees</span></a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</section>
</div>
</template>

<script>
import DataService from "../../../services/DataService"
import WidgetReport from './WidgetReport.vue';
import DateRange from './DateRange.vue';
import ToDoList from './ToDoList.vue';
import CountList from "./Modal/CountList.vue";

export default {
  components: { WidgetReport, DateRange, ToDoList, CountList },

  data() {
       return {
    titleName:'',
    moduleName:'CRM',
    widgets:[],
    viewType:'R',
    fromDate:null,
    toDate:null,
    MenuButton:false,
    toDos:[],
    encCompanyId:null,
    encUserId:null,
    encTrackId:null,
    isLoading:false,
   
   }
},
created:function(){
    this.GetReports();
},
mounted(){
    $("#ddlChangeView a.dropdown-item").click(function () {
        $("#hdnselectedview").val($(this).attr("data-value"));
        if ($(this).attr("data-value") == "R") {
            $("#divReportsdata, #divAddMoreReports").show();
            $("#divReportsdata").addClass("col-md-12").removeClass("col-md-7").show();
            $("#divReportsdata").find(".graph-widget").addClass("col-md-4").removeClass("col-md-6");
            $("#divToDoList").hide();
        }
        else if ($(this).attr("data-value") == "T") {
            // checkVueodolist.ChangeView($(this).attr("data-value"));
            $("#divToDoList").addClass("col-md-12").removeClass("col-md-5").show();
            $("#divReportsdata").find(".graph-widget").addClass("col-md-6").removeClass("col-md-4").removeClass("col-lg-4").removeClass("col-xl-4");
            $("#divReportsdata, #divAddMoreReports").hide();
        }
        else {
            // checkVueodolist.ChangeView($(this).attr("data-value"));
            $("#divToDoList").addClass("col-md-5").removeClass("col-md-12").show();
            $("#divReportsdata").addClass("col-md-7").removeClass("col-md-12").show();
            $("#divReportsdata").find(".graph-widget").addClass("col-md-6").removeClass("col-md-4").removeClass("col-lg-4").removeClass("col-xl-4");
            $("#divReportsdata, #divAddMoreReports").show();
        }
        $("#ddlChangeView .spnChangeView").html($(this).html());
        $('.taskclass').removeClass('col-md-8 col-lg-8').addClass('col-md-4 col-lg-4');
    });
},
methods:{
    OnAddMore(){
     this.$refs.widget.$data.showReports=true;
    },
    FetchCountListData(type){
        this.$refs.countList.$data.FetchData(type,this.$refs.countList.$data.users);
    },
    showDropDowMenu(){
        this.MenuButton=!this.MenuButton;
    },
    GetToDoList(){
        var vm = this;
        vm.isLoading =true;
        DataService.gettoDoList('ModuleName=' + vm.moduleName)
            .then(function (response) {
                vm.isLoading =false;   
                console.log(response);
                vm.encCompanyId=response.data.COMPANY_ID;
                vm.encUserId=response.data.CREATED_BY;
                vm.encTrackId=response.data.TRACK_ID;
                //vm.toDos = response.data.list;
            })
    },
    GetReports(){
        var vm = this;
        vm.isLoading =true;
        DataService.GetReports('moduleName=' + vm.moduleName)
        .then(function (response) {  
                vm.isLoading =false;             
             vm.widgets=response.data.list;
             vm.GetToDoList();
            })
    },
    SetDateRange(from, to) {       
        this.fromDate = from;
        this.toDate = to;
        this.GetReports();
    },
    MyViews(){
        this.$refs.countList.$data.showTeam=false;
        this.$refs.countList.FetchData('kanban', null);
        this.$refs.todolist.FetchDataKanban('kanban', null);
        this.$refs.countList.users = null;
        this.GetReports();
    },
    TeamView(){
        this.$refs.countList.$data.showTeam=true;
        this.$refs.countList.FetchData('kanban', null);
    },
    

}
}
</script>