<template>
    <transition name="modal">
        <loader :is-visible="isLoading"></loader>
        <div class="my-popups">
            <!-- The Modal -->
            <div class="modal my-popups d-block" id="myModal2">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content mw-800">
                        <!-- Modal Header -->
                        <div class="modal-header">
                            <h4 class="modal-title">{{$t('Request')}}{{$t('Time')}}  - {{TaskName}}</h4>
                            <button type="button" v-on:click="$emit('close')" class="close" data-dismiss="modal"><i class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <!-- Modal body -->
                        <div class="modal-body">
                            <Form ref="RequestTimeValidationToDo" id="formId" method="post">
                                <input type="hidden" name="MessageBoardCategoryId" value="" />
                                <input type="hidden" name="MessageBoardCategoryCode" value="" />
                                <input type="hidden" name="ProjectId" v-bind:value="PROJECT_ID" />
                                <input type="hidden" name="RequestType" value="post" />
                                <input type="hidden" name="UserList" v-model="UserList" />
                                <input type="hidden" name="TaskId" v-bind:value="TASK_ID" />
                                <input type="hidden" name="CreatedBy" value="0" />
                                <input type="hidden" name="PostedBy" value="" />
                                <input type="hidden" name="CreatedAt" value="" />
                                <input type="hidden" name="ModifiedBy" value="" />
                                <input type="hidden" name="ModifiedAt" value="" />
                                <input type="hidden" name="StatusId" value="0" />
                                <input type="hidden" name="MessageType" value="s" />
                                <input type="hidden" name="UnreadComments" value="0" />
                                <input type="hidden" name="TotalComments" value="0" />
                                <input type="hidden" name="UserImagePath" value="" />
                                <input type="hidden" name="UserType" value="" />
                                <input type="hidden" name="ProjectName" value="" />
                                <input type="hidden" name="FileCount" value="0" />
                                <input type="hidden" name="IsRead" value="false" />
                                <input type="hidden" name="MessageBoardTitle" v-model="RequestTitle"  />
                                <input type="hidden" name="MessageBoardContent" v-model="ProgressComment"  />
                                <input type="hidden" name="RefSourceName" v-model="RefSourceName"  />
                                <div class="col-md-12 p-0">
                                    <div class="row">
                                        <div class="col-md-6">
                                            <div class="form-group">
                                                <label>{{$t('Title')}}:<span class="text-danger">*</span></label>
                                                <div class="input-group mb-3">
                                                    <Field name="RequestTitle" :rules="{ 'required': true,'max':100 }"
                                                label="Progress Comment" v-slot="{ errors,touched,invalid }">
                                                    <input  type="text"  placeholder="Title" maxlength="100" v-bind:class="{'form-control':true, 'is-invalid':touched && invalid}" data-vv-as="Title" 
                                                    name="RequestTitle" v-model="RequestTitle" />
                                                    <span   name="ProgressComment" class="invalid-feedback">{{ errors[0] }}</span>
                                                    </Field>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-md-6">
                                            <div class="form-group">
                                                <label>{{$t('Time')}}:({{$t('InForRequest')}}{{$t('Hours')}} )<span class="text-danger">*</span></label>
                                                <div class="input-group mb-3">
                                                    <Field name="RequestTitle" :rules="{ 'required': true,'max':4 }"
                                                     label="Progress Comment" v-slot="{ errors,touched,invalid }">
                                                    <input type="text"  data-numberonly="yes" maxlength="4" v-bind:class="{'form-control':true, 
                                                    'is-invalid':touched && invalid}" data-vv-as="Hours" name="RequestHours" v-model="Hours" />
                                                   <span   name="ProgressComment" class="invalid-feedback">{{ errors[0] }}</span>
                                                    </Field>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-md-6">
                                            <div class="form-group">
                                                <label>{{$t('Select')}}:</label>
                                                <div class="form-group pl-0 border-0">
                                                    <div class="form-check form-check-inline">
                                                        <input class="form-check-input" type="radio" v-model="UserType" name="UserType" id="UserType" value="1" v-on:change="onUserType('Client')">
                                                        <label class="form-check-label font-weight-normal" for="UserType">
                                                           {{$t('Client')}}
                                                        </label>
                                                    </div>
                                                    <div class="form-check form-check-inline">
                                                        <input class="form-check-input" type="radio" v-model="UserType" name="UserType" id="UserType" value="2" v-on:change="onUserType('PM')">
                                                        <label class="form-check-label font-weight-normal" for="UserType">
                                                            {{$t('PM')}}
                                                        </label>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-md-6" v-if="ClientUser">
                                            <div class="form-group">
                                                <label> {{$t('SelectClient')}}:<span class="text-danger">*</span></label>                                                
                                                <Field 
                                                    name="ClientTeamValue"
                                                    :rules="ValidateSelectClient(true)"
                                                    label="Name"
                                                    v-slot="{ errors,touched,invalid,changed }">   
                                                      <Multiselect 
                                                        v-bind:class="{ 'vdrop uniquefix_hs bg-white border py-0 pl-0 pr-2': true, 'is-invalid': touched && invalid }"
                                                        v-model="ClientTeamValue"
                                                        placeholder="SelectClient"
                                                        :filter-results="false"
                                                        :object="true"
                                                        label="name"
                                                        :searchable="false"
                                                        track-by="value"
                                                        :show-no-results="false"
                                                        :internal-search="false"
                                                        :loading="isLoading"
                                                        :clear-on-select="false" :close-on-select="true"
                                                        @select="setSelectedValue($event)"
                                                        @update:modelValue="changed"    
                                                        :options="ClientTeam"
                                                    >
                                                    </Multiselect>
                                                    <span   name="ClientTeamValue" class="invalid-feedback d-block">{{ errors[0] }}</span>  
                                                    </Field>
                                             
                                            </div>
                                        </div>
                                        <div class="col-md-6" v-if="PMUser">
                                            <div class="form-group">
                                                <label>{{$t('Select')}} {{$t('PM')}}:<span class="text-danger">*</span></label>
                                                <Field 
                                                    name="PMValue"
                                                    :rules="ValidateSelectPM(false)"
                                                    label="PM"
                                                    v-slot="{ errors,touched,invalid }">   
                                                      <Multiselect 
                                                        v-bind:class="{ 'vdrop uniquefix_hs bg-white border py-0 pl-0 pr-2': true, 'is-invalid': touched && invalid }"
                                                        v-model="PMValue"
                                                        placeholder="SelectPM"
                                                        :filter-results="false"
                                                        :object="true"
                                                        label="name"
                                                        :searchable="false"
                                                        track-by="value"
                                                        :show-no-results="false"
                                                        :internal-search="false"
                                                        :loading="isLoading"
                                                        :clear-on-select="false" :close-on-select="true"
                                                        @select="setSelectedValue($event)"
                                                        @update:modelValue="handleChange"    
                                                        :options="PMTeam"
                                                    >
                                                    </Multiselect>
                                                    <span   name="PMValue" class="invalid-feedback d-block">{{ errors[0] }}</span>  
                                                    </Field>
                                            </div>
                                        </div>
                                        <div class="col-md-6">
                                            <div class="form-group">
                                                <label>{{$t('UploadFile')}}:</label>
                                                <a href="javascript:;" data-toggle="popoveruserguide">
                                                    <i class="fa fa-question-circle-o text-popover" style="font-size: 14px;"></i>
                                                </a>
                                                <div class="input-group">
                                                    <div class="custom-file">
                                                        <input type="file" class="custom-file-inputs" size="4" name="FileSrc" v-bind:class="{'form-control':true}"
                                                         :value="emailinvitation_file" :data-vv-as="$t('Image')" @change="setFile($event)"
                                                               tabindex="7" />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-md-6">
                                            <div class="form-group">
                                                <label for="FormControlTextarea1">{{$t('Description')}}:<span class="text-danger">*</span></label>                                                
                                                <Field name="FormControlTextarea1" :rules="{ 'required': true,'max':100 }"
                                                label="Description" v-slot="{ errors,touched,invalid }">
                                                <textarea  cols="5" v-model="ProgressComment" placeholder="write a comment..."
                                                    v-bind:class="{ 'form-control': true, 'is-invalid': touched && invalid }"/>
                                                <span   name="FormControlTextarea1" class="invalid-feedback">{{ errors[0] }}</span>
                                               </Field>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Form>
                        </div>
                        <div class="modal-footer">
                            <a href="javascript:;" class="btn btn-success save-btn" v-on:click="RequestTimeSubmit()"><i class="fa fa-save pr-2"></i><span>{{$t('submit')}}</span></a>
                            <a href="javascript:;" class="btn btn-danger delete-btn" v-on:click="RequestTimeCancel()"><i class="fa fa-close pr-2"></i><span>{{$t('Cancel')}}</span></a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
  
</template>
<script>
import DataService from '../../../../services/DataService';
export default{
    data: function () {
            return {
                isLoading:false,
                RequestTitle: '',
                Hours: '',
                emailinvitation_file: '',
                ProgressComment: '',
                ClientTeamValue: [],
                PMValue: [],
                TRACKED_HOURS: '',
                PROJECT_ID: '',
                TASK_ID: '',
                UserList: [],
                ClientTeam: [],
                PMTeam: [],
                SelectedUserList: [],
                UserType: 2,
                ClientUser: false,
                PMUser: true,
                RefSourceName: 'RequestTime',
                TaskName: '',
                file:null
            }
        },
        created: function () {

            this.TRACKED_HOURS = this.$parent.SelectedItem.TRACKED_HOURS;
            this.PROJECT_ID = this.$parent.SelectedItem.PROJECT_ID;
            this.TASK_ID = this.$parent.SelectedItem.TASK_ID;
            this.ClientTeam = this.$parent.SelectedItem.ClientTeam;
            this.PMTeam = this.$parent.SelectedItem.PMTeam;
            this.ValidateSelectClient(false);
            this.ValidateSelectPM(true);
            setTimeout("RadioButtonBootstrap()", 500);
            // setTimeout("ShowPopOver()", 300);
            setTimeout("ShowfileUploadWithCulture();", 500);
            this.TaskName = this.$parent.SelectedItem.TASK_NAME;
            if (typeof (this.ClientTeam) == 'undefined' || this.ClientTeam == null || typeof (this.PMTeam) == 'undefined' || this.PMTeam == null) {
                this.Teams(null, 'get');
            }
        },
        methods: {
            setFile:function(e){
    this.file=e.target.files[0];
    },
            setSelectedValue: function (event) {
                var vm = this;
                var data = [];
                vm.UserList = [];
                if (event.length > 0) {
                    $.each(event, $.proxy(function (index, clientteam) {
                        data.push(clientteam.Id);
                    }));
                    vm.UserList = data.join(',');
                }
            },
            ValidateSelectClient: function (value) {
                var obj = {};
                return obj['required'] = value;
            },
            ValidateSelectPM: function (value) {
                var obj = {};
                return obj['required'] = value;
            },
            onUserType: function (type) {
                var vm = this;
                if (type == 'PM') {
                    vm.ClientUser = false;
                    vm.PMUser = true;
                    vm.ValidateSelectClient(false);
                    vm.ValidateSelectPM(true);
                }
                else {
                    vm.ClientUser = true;
                    vm.PMUser = false;
                    vm.ValidateSelectClient(true);
                    vm.ValidateSelectPM(false);
                }
            },
            settime: function () {
                var thisObj = this;
                if (thisObj.Hours.indexOf(':') == -1) {
                    thisObj.Hours = thisObj.Hours.trim();
                    if (thisObj.Hours.length == 0) {
                        return false;
                    }
                    else {
                        if (isNaN(thisObj.Hours)) {
                            return false;
                        }
                        else {
                            thisObj.Hours = thisObj.Hours + ':00';
                        }
                    }
                }
                else {
                    return false;
                }
            },
            Teams: function (item, type) {
                var vm = this;
                var prjId = vm.PROJECT_ID;
                if (type == 'get') {
                    vm.ClientTeam = [];
                    vm.PMTeam = [];
                }
                DataService.UserToDoList('projectId=' + prjId).then(function (response) {
                    var objresult = JSON.parse(response.data.bodyText);
                    var clientGroup = $.grep(objresult.result, function (element, index) {
                        return element.UserTypeId == 4;
                    });
                    var pmGroup = $.grep(objresult.result, function (element, index) {
                        return element.UserTypeId == 2 || element.UserTypeId == 1;
                    });
                    var obj = {};
                    var obj1 = {};
                    $.each(clientGroup, $.proxy(function (index, childItem) {
                        $.each(childItem.Userses, $.proxy(function (index, clientchildItem) {
                            obj = {
                                Id: clientchildItem.Id,
                                Name: clientchildItem.Name,
                            };
                            if (type == 'get') {
                                vm.ClientTeam.push(obj);
                            }
                            else {
                                item.ClientTeam.push(obj);
                            }
                        }));
                    }));
                    if (type == 'get') {
                        this.$parent.SelectedItem.ClientTeam = vm.ClientTeam;
                    }
                    $.each(pmGroup, $.proxy(function (index, childItem1) {
                        $.each(childItem1.Userses, $.proxy(function (index, pmchildItem) {
                            obj1 = {
                                Id: pmchildItem.Id,
                                Name: pmchildItem.Name,
                            };
                            if (type == 'get') {
                                vm.PMTeam.push(obj1);
                            }
                            else {
                                item.PMTeam.push(obj1);
                            }
                        }));

                    }));
                    if (type == 'get') {
                        this.$parent.SelectedItem.PMTeam = vm.PMTeam;
                    }
                });

                setTimeout("ShowfileUploadWithCulture();", 550);
            },
            RequestTimeSubmit: function () {
                var vm = this;
                var data = [];
                const formdata = new FormData();
                formdata.append("file", vm.file);   
                vm.$validator.RequestTimeValidationToDo.validate().then((result) => {   
                    if (result.valid) {
                        vm.isLoading=true; 
                        if (vm.PMTeam != null && vm.PMTeam != '' && vm.PMTeam.length > 0) {
                            $.each(vm.PMTeam, $.proxy(function (index, comma) {
                                data.push(comma.Id);
                            }));
                        }
                        $("input[name='UserList']").val(vm.UserList);
                         DataService.MessagesPost(formdata).then(function(data){
                            if (data.error == "0") {

var time = vm.Hours;
var hours = time;
var RequestTimeSec = parseFloat((parseFloat(hours, 10) * 60 * 60), 10);

DataService.MessagesRequestTime({
        title: vm.RequestTitle, comment: vm.ProgressComment, msgboardid: data.msgBoardId, projectid: vm.PROJECT_ID, taskid: vm.TASK_ID, request_time_sec: RequestTimeSec
    }).then(function(response){
        var obj = response;
        if (obj.result.GROUP_ID != null && obj.result.GROUP_ID != "") {
            vm.RequestTitle = '';
            vm.ProgressComment = '';
            vm.Hours = '';
            vm.UserList = [];
            vm.ClientTeamValue = [];
            vm.PMValue = [];
            vm.emailinvitation_file = '',
            vm.RequestTimeBtn = false;
            vm.$validator.reset();
            vm.$emit('close');
            vm.$parent.FetchDataKanban('kanban', null);
            vm.ShowAlert(vm.$t("RequestTimeSubmited"), "success", true, vm.$t("Alert"));
            vm.isLoading=false;
        }
        else {
            vm.ShowAlert(vm.$t("Something_Wrong"), "failure", true, vm.$t('Alert'));
            vm.isLoading=false;
        }
    })
}
else {
vm.ShowAlert(data.msg, "failure", true, vm.$t("Alert"));
vm.isLoading=false;
}
                         })
                        
                    }
                    else {
                        vm.isLoading=false;
                    }
                });
            },
            RequestTimeCancel: function () {
                var vm = this;
                vm.ProgressComment = '';
                vm.RequestTimeBtn = false;
                vm.$emit('close');
            },
        }
}
</script>