                                        <div class="col-md-12">
                                            <label for="zone name">{{$t('Comments')}}:<span class="mandatory">*</span></label>
                                            <textarea type="text" maxlength="500" v-bind:class="{'form-control':true, 'is-invalid':errors.has('RequestTimeComment.CommentText')}" v-model="CommentText" v-validate="'required'"></textarea>
                                            <span v-show="errors.has('RequestTimeComment.CommentText')" class="invalid-feedback">@string.Format({{$t('Required')}}, {{$t('Comments')}})</span>
                                        </div>
                                    </div>
                                    <div class="row flex-row-reverse mt-2">
                                        <div class="col-lg-6 text-right">
                                            <a href="javascript:;" :title="$t('Save')" class="btn btn-success text-white" v-on:click="SaveRequestComment()"><i class="fa fa-save pr-2"></i> {{$t('Save')}}</a>
                                            <a href="javascript:;" :title="$t('Cancel')" v-on:click="$emit('close')" class="btn btn-danger"><i class="fa fa-close pr-2"></i> {{$t('Cancel')}}</a>
                                        </div>
                                        <div class="col-lg-6">
                                            <!-- <div class="py-2">@HtmlHelpers.MandatoryStringFull("R")</div> -->
                                        </div>
                                    </div>
                                    <div class="row">
                                        <div class="col-md-12" id="requestlogcommentlistingDiv">
                                        </div>
                                    </div>
                                </div>
                            </Form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
    
</template>
<script>
import DataService from '../../../../services/DataService';

export default{
    data() {
            return {
                ImageAvatar: '/Content/images/DefaultImages/avatar-icon.jpg',
                ImageUser: '',
                CreatedBy: '',
                CreatedAt: '',
                MessageBoardId: '',
                CommentText: '',
                CommentData: null,
                Title: '',
                noRecord: false,
                RefSourceName: 'TaskComment',
                PROJECT_ID: 0,
                UserList: [],
                TASK_ID: 0,
                isLoading:false
            }
        },
        created: function () {
            var vm = this;
            vm.MessageBoardId = vm.$parent.SelectedItem.MESSAGE_BOARD_ID;
            vm.CommentData = vm.$parent.SelectedItem;
            vm.CreatedAt = vm.$parent.SelectedItem.CREATED_AT;
            vm.Title = vm.$parent.SelectedItem.TASK_NAME;
            vm.PROJECT_ID = vm.$parent.SelectedItem.PROJECT_ID;
            //vm.UserList = vm.$parent.SelectedItem.TASK_NAME;
            vm.TASK_ID = vm.$parent.SelectedItem.TASK_ID;
            GetRequestTimeComment(vm.$parent.SelectedItem.TASK_ID);
        },
        methods: {
            formatDateTime: function (value) {
                if (value) {
                    try {
                        var timeFormat = "h:mm tt";
                        timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
                        var getDate = new Date(moment(String(value)).year(), moment(String(value)).month(), moment(String(value)).date(), moment(String(value)).hours(), moment(String(value)).minutes());
                        return moment(getDate, "YYYY-MM-DD HH:mm:ss").format(String('M/d/yyyy').toUpperCase() + " " + String(timeFormat));
                    } catch (e) {
                        var timeFormat = "h:mm tt";
                        timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
                        return moment(String(value)).format(String('M/d/yyyy').toUpperCase() + " " + String(timeFormat));
                    }
                }
            },
            SaveMessageData: function () {
                var vm = this;
                var params='';
                DataService.MessagePost(params).then(function(response){
                    if (response.error == "0") {
                            vm.MessageBoardId = response.msgBoardId;
                            DataService.MessagesRequestTime({ title: vm.Title, comment: vm.CommentText, msgboardid: data.msgBoardId, projectid: vm.$parent.SelectedItem.PROJECT_ID, taskid: vm.$parent.SelectedItem.TASK_ID, request_time_sec: 0 })
                            .then(function (response1) {
                                if (response1.result.GROUP_ID != null && response1.result.GROUP_ID != "") {
                                    vm.SaveRequestCommentData();
                                    vm.isLoading=false;
                                }
                                else {
                                    vm.isLoading=false;
                                }
                            })
                        }
                        else {
                            vm.isLoading=false;
                        }
                })                
            },
            SaveRequestCommentData: function () {
                var vm = this;
                var obj = {
                    MessageBoardId: vm.MessageBoardId,
                    Title: vm.Title,
                    Comment: vm.CommentText,
                };
                DataService.SaveCommentLog(obj).then(function(result){
                    if (result.data.result.ID > 0) {
                            vm.ShowAlert(vm.$t('CreatedSuccessfully',[vm.$t('Comment')]), "success", true, vm.$t('Alert'));
                            vm.$emit('close');
                            vm.$parent.FetchDataKanban('kanban', null);
                        }
                })            
            },
            SaveRequestComment: function () {
                var vm = this;
                vm.$ref.RequestTimeComment.validate().then((result) => {   
                    if (result.valid) {
                        if (vm.MessageBoardId > 0) {
                            vm.SaveRequestCommentData();
                        }
                        else {
                            vm.SaveMessageData();
                        }
                    }
                });
            }
        },
}
</script>